<template>
    <div class="container">
        <div class="header">
            <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item>优惠券</el-breadcrumb-item>
                <el-breadcrumb-item>优惠券列表</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="button-wrapper">
            <el-button type="primary" @click="addNew()">创建优惠券</el-button>
        </div>
        <div class="search-wrapper">
            <el-form :inline="true" :model="searchParams" class="demo-form-inline">
                <el-form-item label="优惠券名称">
                    <el-input v-model="searchParams.couponName" placeholder="优惠券名称"></el-input>
                </el-form-item>
                <el-form-item label="优惠券状态">
                    <el-select v-model="searchParams.couponStatus" clearable placeholder="优惠券状态">
                        <el-option v-for="(value, key) in classList" :key="key" :label="value" :value="key"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="search">查询</el-button>
                    <el-button @click="clear">清空</el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="list-wrapper">
            <el-table :data="list" style="width: 100%">
                <el-table-column type="index" label="序号" width="50"></el-table-column>
                <el-table-column prop="couponName" label="优惠券名称"></el-table-column>
                <el-table-column label="已领取/剩余">
                    <template slot-scope="scope">
                        <router-link :to="{name:'couponDetails',params:{id:scope.row.id}}">
                            <el-button type="text" size="small">{{scope.row.sendCount}}/{{scope.row.totalCount-scope.row.sendCount}}</el-button>
                        </router-link>
                    </template>
                </el-table-column>
                <el-table-column prop="couponStatus" label="状态" :formatter="statusFormat"></el-table-column>
                <el-table-column prop="remarks" label="备注"></el-table-column>
                <el-table-column label="操作" width="180">
                    <template slot-scope="scope">
                        <div v-if="scope.row.couponStatus==2">
                            <el-button @click="del(scope.row.id)"  type="text" size="small">删除</el-button>
                        </div>
                        <div v-else>
                            <el-button @click="edit(scope.row)"  type="text" size="small">编辑</el-button>
                            <el-button @click="toLink(scope.row.couponLink)"  type="text" size="small">链接</el-button>
                            <el-button @click="invalid(scope.row.id)"  type="text" size="small">作废</el-button>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <el-pagination
                background
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-size="searchParams.pageSize"
                layout="prev, pager, next, jumper"
                :total="total"
        ></el-pagination>
        <!--编辑-->
        <el-dialog title="编辑" :visible.sync="dialogFormVisible">
            <el-form :model="form" label-width="100px" ref="dialogForm">
                <el-form-item label="优惠券名称" prop="couponName">
                    <el-input v-model="form.couponName"></el-input>
                </el-form-item>
                <el-form-item label="发放总量" prop="totalCount">
                    <el-input v-model="showForm.totalCount" disabled></el-input>
                </el-form-item>
                <el-form-item label="使用门槛" prop="thresholdType">
                    <el-radio-group v-model="showForm.thresholdType" size="mini">
                        <el-radio :label="0" disabled>无门槛</el-radio>
                        <div style="margin-top: 10px;">
                            <el-radio :label="1" disabled>订单满</el-radio>
                        </div>
                    </el-radio-group>
                    <div class="left_flex" v-if="showForm.thresholdType==1">
                        <el-input v-model="showForm.thresholdAmount" disabled><template slot="append">元</template></el-input>
                    </div>
                </el-form-item>
                <el-form-item label="优惠内容" prop="preferentialAmount">
                    <el-input v-model="showForm.preferentialAmount" disabled>
                        <template slot="prepend">减</template>
                        <template slot="append">元</template>
                    </el-input>
                </el-form-item>
                <el-form-item label="有效期" prop="order_valid">
                    <el-radio-group v-model="showForm.periodType" size="mini">
                        <div style="display: flex">
                            <el-radio :label="0" disabled>日期</el-radio>
                            <div style="display: flex;flex: 1;margin-left: 20px;height: 20px;" v-if="showForm.periodType==0">
                                <el-input v-model="showForm.periodStartTime" disabled style="flex: 1"></el-input><span style="font-size: 14px;line-height: 40px;width: 40px;text-align: center">至</span><el-input v-model="showForm.periodEndTime" disabled style="flex: 1"></el-input>
                            </div>
                        </div>
                        <div style="margin-top: 10px;display: flex;height: 20px;">
                            <el-radio :label="1" disabled>领券后</el-radio>
                            <el-input v-model="showForm.periodType" v-if="showForm.periodType==1" disabled style="flex: 1;margin-top: -25px;"><template slot="append">天</template></el-input>
                        </div>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="适用商品" prop="count">
                    <el-button @click="dialogFormVisible2=true"  type="text" size="small">{{showForm.count}}</el-button>
                </el-form-item>
                <el-form-item label="领取人" prop="targetUsers">
                    <el-radio-group v-model="showForm.targetUsers" size="mini">
                        <el-radio :label="1" disabled>全部</el-radio>
                        <el-radio :label="2" disabled>新用户</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="领取限制" prop="receiveLimit">
                    <el-radio-group v-model="showForm.receiveLimit" size="mini">
                        <el-radio :label="-1" disabled>不限</el-radio>
                        <div style="margin-top: 10px;">
                            <el-radio :label="1" disabled>次数</el-radio>
                        </div>
                    </el-radio-group>
                    <div class="left_flex" v-if="showForm.receiveLimit==1">
                        <el-input v-model="showForm.receiveLimit" disabled></el-input>
                    </div>
                </el-form-item>
                <el-form-item label="备注" prop="remarks">
                    <el-input v-model="form.remarks"></el-input>
                </el-form-item>
                <el-form-item label="使用说明" prop="useDescription">
                    <el-input type="textarea" v-model="form.useDescription"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitCoupon">确 定</el-button>
            </div>
        </el-dialog>
        <!--已添加商品-->
        <el-dialog title="已添加商品" :visible.sync="dialogFormVisible2" width="80%">
            <div class="search-wrapper">
                <el-form :inline="true" :model="searchParams2" class="demo-form-inline">
                    <el-form-item label="商品货号">
                        <el-input v-model="searchParams2.goodsId" placeholder="商品货号"></el-input>
                    </el-form-item>
                    <el-form-item label="商品名称">
                        <el-input v-model="searchParams2.goodsName" placeholder="商品名称"></el-input>
                    </el-form-item>
                    <el-form-item label="商家">
                        <el-input v-model="searchParams2.suppliersName" placeholder="商家"></el-input>
                    </el-form-item>
                    <el-form-item label="商品分类">
                        <el-cascader
                                v-model="value"
                                :options="options"
                                @change="handleChange"
                                placeholder="商品分类" clearable></el-cascader>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="search2">查询</el-button>
                        <el-button @click="clear2">清空</el-button>
                    </el-form-item>
                </el-form>
            </div>
            <div class="list-wrapper">
                <el-table :data="list2" style="width: 100%">
                    <el-table-column prop="goodsName" label="商品名称"></el-table-column>
                    <el-table-column prop="goodsId" label="商品ID"></el-table-column>
                    <el-table-column prop="catName" label="商品分类"></el-table-column>
                    <el-table-column prop="shopPrice" label="售价"></el-table-column>
                    <el-table-column prop="tokenPrice" label="积分抵扣"></el-table-column>
                    <el-table-column prop="suppliersName" label="商家"></el-table-column>
                </el-table>
            </div>
            <el-pagination
                    background
                    @current-change="handleCurrentChange2"
                    :current-page="currentPage2"
                    :page-size="searchParams2.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="total2"
            ></el-pagination>
        </el-dialog>
    </div>
</template>

<script>
import { debug } from 'util';
    export default {
        name: "couponList",
        data() {
            return {
                total: 0,
                currentPage: 1,
                searchParams: {pageNumber: 1, pageSize: 10,couponName:'',couponStatus:''},
                list: [],
                classList:{
                    0:'未开始',
                    1:'进行中',
                    2:'已失效',
                },
                dialogFormVisible:false,
                dialogFormVisible2:false,
                form:{id:'',couponName:'',remarks:'',useDescription:''},
                showForm:{},
                total2: 0,
                currentPage2: 1,
                searchParams2: { pageNumber: 1, pageSize: 10,couponId:'',goodsId:'',goodsName:'',suppliersName:'',catId:'',catType:''},
                list2: [],
                value:[],
                options: [],
            }
        },
        methods:{
            getList(){
                this.$api.get('hd/itzCoupon/list', this.searchParams, res => {
                    if(res.status==200){
                        this.list=res.data.rows
                        this.total = res.data.total
                    }
                })
            },
            getList2(){
                this.$api.get('hd/itzCouponGoods/goods/list',this.searchParams2,res=>{
                    if(res.status==200){
                        this.list2=res.data.rows;
                        this.total2 = res.data.total
                    }
                })
            },
            getProductList(){
                this.$api.get('hd/itzCoupon/category',null,res=>{
                    if(res.status==200){
                        let options=[]
                        res.data.rows.forEach(value=>{
                            let obj={
                                value:value.catId,
                                label:value.catName
                            }
                            if(value.itzCategories.length>0){
                                obj.children=[]
                                value.itzCategories.forEach(subValue=>{
                                    let subObj={
                                        value:subValue.catId,
                                        label:subValue.catName
                                    }
                                    if(subValue.itzCategories.length>0){
                                        subObj.children=[]
                                        subValue.itzCategories.forEach(sonValue=>{
                                            let sonObj={
                                                value:sonValue.catId,
                                                label:sonValue.catName
                                            }
                                            subObj.children.push(sonObj)
                                        })
                                    }
                                    obj.children.push(subObj)
                                })
                            }
                            options.push(obj)
                        })
                        this.options=options
                    }
                })
            },
            statusFormat(row, column, cellValue){
                return this.classList[cellValue]
            },
            edit(info){
                this.dialogFormVisible=true
                this.form.id=info.id
                this.form.couponName=info.couponName
                this.form.remarks=info.remarks
                this.form.useDescription=info.useDescription
                this.$api.get(`hd/itzCoupon/info/${info.id}`,null,res=>{
                    if(res.status==200){
                        this.showForm=res.data.coupon
                        this.showForm.periodType=res.data.coupon.periodType==0?0:1
                        this.showForm.periodStartTime=this.utils.formatDate('YYYY-MM-DD HH:mm:ss', res.data.coupon.periodStartTime * 1e3)
                        this.showForm.periodEndTime=this.utils.formatDate('YYYY-MM-DD HH:mm:ss', res.data.coupon.periodEndTime * 1e3)
                        this.showForm.count=res.data.count
                        this.showForm.receiveLimit=res.data.coupon.receiveLimit==-1?-1:1
                    }
                })
                this.searchParams2.couponId=info.id
                this.getList2()
            },
            toLink(links){
                // links='https://www.huanhuanyiwu.com'
                this.$alert(links, '优惠券名称', {
                    confirmButtonText: '复制'
                }).then(() => {
                    let tag=document.createElement('input')
                    tag.value=links
                    document.body.appendChild(tag)
                    tag.select()
                    document.execCommand('copy')
                    tag.remove()
                    this.$message({
                        type: 'success',
                        message: '已复制到剪切板!'
                    })
                })
            },
            invalid(id){
                this.$confirm('确定作废该优惠券吗?(已领取的可继续使用)', '作废', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(() => {
                    this.$api.put('hd/itzCoupon/status',{id:id,status:2},res=>{
                        if(res.status==200){
                            this.getList()
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消作废'
                    });
                });
            },
            del(id){
                this.$confirm('确定删除该优惠券吗?', '删除', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(() => {
                    this.$api.put('hd/itzCoupon/status',{id:id,status:3},res=>{
                        if(res.status==200){
                            this.getList()
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            submitCoupon(){
                this.$api.put('hd/itzCoupon/coupon',this.form,res=>{
                    if(res.status==200){
                        this.dialogFormVisible=false
                        this.getList()
                    }
                })
            },
            handleCurrentChange(currentPage) {
                this.searchParams.pageNumber = currentPage
                this.getList()
            },
            clear() {
                this.searchParams.pageNumber = 1
                this.searchParams.couponName = ''
                this.searchParams.couponStatus = ''
                this.getList()
            },
            search() {
                this.searchParams.pageNumber = 1;
                this.getList()
            },
            addNew(){
                this.$router.push({ name: 'newCoupon'})
            },
            handleCurrentChange2(currentPage) {
                this.searchParams2.pageNumber = currentPage
                this.getList2()
            },
            handleChange(value) {
                this.searchParams2.catId=value[value.length-1]
                this.searchParams2.catType=value.length
            },
            clear2() {
                this.searchParams2.pageNumber = 1
                this.searchParams2.goodsName = ''
                this.searchParams2.suppliersName = ''
                this.searchParams2.goodsId = ''
                this.searchParams2.catId = ''
                this.searchParams2.catType = ''
                this.value=[]
                this.getList2()
            },
            search2() {
                this.searchParams2.pageNumber = 1
                this.getList2()
            }
        },
        created() {
            this.getList()
            this.getProductList()
        }
    }
</script>

<style lang="scss" scoped>
    .container {
        font-size: 15px;
        .header{
            margin-bottom: 10px;
        }
        .button-wrapper {
            text-align: right;
            margin-bottom: 10px;
        }
        .search-wrapper {
            padding: 10px 10px 0;
            margin-bottom: 10px;
            background: #fff;
            border: 1px solid #eee;
            .el-form-item {
                margin-bottom: 10px;
            }
        }
        .el-radio{
            margin-right: 10px;
        }
        .left_flex{
            display: inline-block;
            vertical-align: text-top;
        }
        .input-box{
            position: relative;
        }
        .input-box input{
            opacity: 0;
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 32px;
        }
    }
    .el-pagination {
        text-align: center;
        margin-top: 10px;
    }
</style>
