<template>
  <div>
    <div class="header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>{{ name }}平台</el-breadcrumb-item>
        <el-breadcrumb-item>首页</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <ul>
        <li>
          <span>浣豆余额：{{ balance }} 个</span>
          <el-button type="text" @click="$router.push({ name: 'AccountDetails' })">账户明细</el-button>
        </li>
        <li>
          <span>已代付总额：{{ pay }} 浣豆</span>
        </li>
        <li>
          <span>出借人：{{ lender }} 人</span>
          <el-button type="text" @click="$router.push({ name: 'LenderList' })">查看</el-button>
        </li>
      </ul>
      <div class="note">充值请联系{{ storeName }}商城工作人员</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomePage',
  data() {
    return {
      name: '',
      balance: 0,
      pay: 0,
      lender: 0,
      params: {
        page: 1,
        limit: 10
      }
    }
  },
  methods: {
    getList() {
      let pramstr = JSON.stringify(this.params)
        .replace(/:/g, '=')
        .replace('{', '')
        .replace('}', '')
        .replace(/,/g, '&')
        .replace(/"/g, '')
        .replace(/\s+/g, '')
      this.$api.get('hd/itzPartner/page/list?' + pramstr, null, res => {
        if (res.status == 200) {
          this.name = res.data.rows[0].partnerName
          this.balance = res.data.rows[0].token
          this.pay = res.data.rows[0].useToken
          this.lender = res.data.rows[0].lendNum
          localStorage.setItem('partnerName', this.name)
        }
      })
    }
  },
  created() {
    this.getList()
  }
}
</script>

<style lang="scss" scoped>
.content {
  margin-top: 10px;
  background-color: #fff;
  padding: 10px;
  ul {
    overflow: hidden;
    margin: 0;
    padding: 0;
    li {
      list-style: none;
      margin-right: 50px;
      line-height: 40px;
      float: left;
      span {
        margin-right: 20px;
      }
    }
  }
  .note {
    color: #999;
  }
}
</style>
