<!-- 账户余额明细 -->
<template>
  <div class="account-balance-container">
    <!-- header -->
    <page-title first-title="账户信息" second-title="账户余额明细"></page-title>
    <div class="list-wrapper">
      <el-tabs type="border-card" @tab-click="changeTab" v-model="currentTab">
        <el-tab-pane label="现金" name="1">
          <el-table :data="balanceData" border stripe :header-cell-style="{ background: '#eef1f6' }">
            <el-table-column label="发生时间" prop="addtime" :formatter="fmtTime"></el-table-column>
            <el-table-column label="类型" prop="type_name"></el-table-column>
            <el-table-column label="备注" prop="remark"></el-table-column>
            <el-table-column label="金额" prop="format_cash" :formatter="fmtMoney"> </el-table-column>
            <el-table-column label="余额" prop="current_cash_money"></el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="积分" name="0" v-if="cooperateType == 1">
          <el-table :data="balanceData" border stripe :header-cell-style="{ background: '#eef1f6' }">
            <el-table-column label="发生时间" prop="addtime" :formatter="fmtTime"></el-table-column>
            <el-table-column label="类型" prop="type_name"></el-table-column>
            <el-table-column label="备注" prop="remark"></el-table-column>
            <el-table-column label="金额" prop="format_huanbi" :formatter="fmtMoney"></el-table-column>
            <el-table-column label="余额" prop="current_huanbi_money"></el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </div>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page.sync="params.page"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getAccountBalanceData } from '../../api/settle'
export default {
  name: '',
  data() {
    return {
      currentTab: '1',
      balanceData: [],
      params: {
        suppliers_id: '',
        page: 1,
        size: 10,
        cash_is: 1
      },
      total: 0
    }
  },
  computed: {
    ...mapState({
      suppliers_id: state => state.suppliers.suppliers_id,
      cooperateType: state => state.suppliers.cooperateType
    }),
    fmtParams() {
      this.params.cash_is = Number(this.currentTab)
      this.params.suppliers_id = this.suppliers_id
      return this.params
    }
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      getAccountBalanceData(this.fmtParams).then(res => {
        this.balanceData = res.list
        this.total = res.paged.total
      })
    },
    changeTab() {
      this.balanceData = []
      this.params.page = 1
      this.getData()
    },
    handleCurrentChange() {
      this.getData()
    },
    fmtTime(row, column, cellValue) {
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', cellValue * 1e3)
    },
    fmtMoney(row, column, cellValue) {
      let _d = row['direction'] == 1 ? '+' : '-'
      return cellValue > 0 ? _d + cellValue : 0
    }
  }
}
</script>

<style lang="scss" scoped>
.list-wrapper {
  margin-top: 20px;
  background-color: #fff;
}
.el-pagination {
  margin-top: 15px;
  text-align: center;
}
</style>
