<template>
  <div class="container">
    <section>
      <div class="btn-wrapper">
        <el-button @click="goBack">返回</el-button>
      </div>
      <div class="table-head">
        <h1>状态：{{ status }}</h1>
      </div>
      <div class="table-wrapper">
        <template>
          <el-button class="right" @click="confirm" v-if="settleInfo.status === STATUS.UNCONFIRM">确认</el-button>
          <el-button class="right" type="info" v-else disabled>已确认</el-button>
        </template>
        <h2 class="t-center">结算单</h2>
        <p class="t-center" v-if="settleInfo.status === STATUS.UNCONFIRM">
          结算时间：{{ (settleInfo.settlement_start * 1e3) | convertTime }}——{{
            (settleInfo.settlement_end * 1e3) | convertTime
          }}
        </p>
        <div class="t-head">
          <p class="line-item"><label>创建时间：</label>{{ (settleInfo.addtime * 1e3) | convertTime }}</p>
          <p class="line-item"><label>收款单位：</label>{{ settleInfo.pay_company }}</p>
          <p class="line-item"><label>收款人：</label>{{ settleInfo.payee }}</p>
          <p class="line-item">
            <label v-if="settleInfo.remit_type === PAYTYPE.ALIPAY">支付宝账号：</label>
            <label V-else>银行账户：</label>
            {{ settleInfo.remit_account }}
          </p>
        </div>
        <div class="t-body clearfix">
          <template v-if="settleInfo.commission_type === COMMISSIONTYPE.SLAES">
            <div class="t-body-left">
              <h2>现金</h2>
              <p>
                <span>待确认金额（小写）</span
                ><label>{{ utils.formatMoney(settleInfo.all_cash_income - settleInfo.all_cash_expend) }}</label>
              </p>
              <p>
                <span>销售额佣金（全款）</span><label>{{ utils.formatMoney(settleInfo.sales_service_fee) }}</label>
              </p>
              <p>
                <span>抽取保证金</span><label>{{ utils.formatMoney(settleInfo.bond) }}</label>
              </p>
              <p>
                <span>实际结算金额（小写）</span><label>{{ utils.formatMoney(settleInfo.cash_real_pay) }}</label>
              </p>
              <p>
                <span>金额（大写）</span><label>{{ this.utils.getCapMoney(settleInfo.cash_real_pay) }}</label>
              </p>
            </div>
            <div class="t-body-right" v-if="cooperateType == 1">
              <h2>积分</h2>
              <p>
                <span>待确认金额（小写）</span
                ><label>{{ utils.formatMoney(settleInfo.all_huanbi_income - settleInfo.all_huanbi_expend) }}</label>
              </p>
              <p>
                <span>实际结算金额 （小写）</span><label>{{ utils.formatMoney(settleInfo.huanbi_real_pay) }}</label>
              </p>
              <p>
                <span>金额（大写）</span><label>{{ this.utils.getCapMoney(settleInfo.huanbi_real_pay) }}</label>
              </p>
            </div>
          </template>
          <template v-else>
            <div class="t-body-left">
              <h2>现金</h2>
              <p>
                <span>待确认金额（小写）</span
                ><label>{{ utils.formatMoney(settleInfo.all_cash_income - settleInfo.all_cash_expend) }}</label>
              </p>
              <p>
                <span>扣除佣金（现金）</span><label>{{ utils.formatMoney(settleInfo.cash_service_fee) }}</label>
              </p>
              <p>
                <span>扣除交易费用（现金）</span><label>{{ utils.formatMoney(settleInfo.cash_income_fee) }}</label>
              </p>
              <p>
                <span>抽取保证金（现金）</span><label>{{ utils.formatMoney(settleInfo.bond) }}</label>
              </p>
              <p>
                <span>扣除打款费用（现金）</span><label>{{ utils.formatMoney(settleInfo.cash_remit_fee) }}</label>
              </p>
              <p>
                <span>实际结算金额（小写）</span><label>{{ utils.formatMoney(settleInfo.cash_real_pay) }}</label>
              </p>
              <p>
                <span>金额（大写）</span><label>{{ this.utils.getCapMoney(settleInfo.cash_real_pay) }}</label>
              </p>
            </div>
            <div class="t-body-right" v-if="cooperateType == 1">
              <h2>积分</h2>
              <p>
                <span>待确认金额（小写）</span
                ><label>{{ utils.formatMoney(settleInfo.all_huanbi_income - settleInfo.all_huanbi_expend) }}</label>
              </p>
              <p>
                <span>扣除积分佣金（个）</span><label>{{ utils.formatMoney(settleInfo.huanbi_service_fee) }}</label>
              </p>
              <p>
                <span>扣除积分交易费用（人）</span><label>{{ utils.formatMoney(settleInfo.huanbi_income_fee) }}</label>
              </p>
              <p>
                <span>扣除积分打款费用（人）</span><label>{{ utils.formatMoney(settleInfo.huanbi_remit_fee) }}</label>
              </p>
              <p>
                <span>实际结算金额 （小写）</span><label>{{ utils.formatMoney(settleInfo.huanbi_real_pay) }}</label>
              </p>
              <p>
                <span>金额（大写）</span><label>{{ this.utils.getCapMoney(settleInfo.huanbi_real_pay) }}</label>
              </p>
            </div>
          </template>
        </div>
        <h3 v-if="settleInfo.status" style="padding-left: 20px">
          结算时间：{{ (settleInfo.settlement_start * 1e3) | convertTime }}——{{
            (settleInfo.settlement_end * 1e3) | convertTime
          }}
        </h3>
        <div class="t-bottom clearfix">
          <div class="t-bottom-left">待结算编号</div>
          <p class="t-bottom-right">
            <span v-for="(item, index) in settleInfo.statement_no" :key="index">{{ item }}</span>
          </p>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { STATUS, STATUSLIST, PAYTYPE, COMMISSIONTYPE } from './static'
import { getStatementInfo, confirmSettlement } from '../../api/settle'
export default {
  name: 'SettlementInfo',
  data() {
    return {
      STATUS: STATUS,
      PAYTYPE: PAYTYPE,
      COMMISSIONTYPE: COMMISSIONTYPE,
      multi_statement_id: this.$route.params.id ? this.$route.params.id : '', //汇总单id
      settleInfo: {}
    }
  },
  created() {
    this.getStatementInfo()
  },
  computed: {
    ...mapState({
      cooperateType: state => state.suppliers.cooperateType
    }),
    status() {
      return STATUSLIST[this.settleInfo.status]
    }
  },
  methods: {
    ...mapMutations({
      saveCooperateType: 'saveCooperateType'
    }),
    getStatementInfo() {
      getStatementInfo(this.multi_statement_id).then(
        res => {
          this.settleInfo = res
          this.saveCooperateType(res.cooperate_type)
        },
        error => {
          console.log(error)
        }
      )
    },
    confirm() {
      let data = {}
      data['multi_statement_id'] = this.settleInfo.multi_statement_id
      data['bond'] = this.settleInfo.bond
      data['use_cash_service_fee'] = Number(this.settleInfo.cash_service_fee) ? 1 : 0
      data['use_cash_income_fee'] = Number(this.settleInfo.cash_income_fee) ? 1 : 0
      data['use_cash_remit_fee'] = Number(this.settleInfo.cash_remit_fee) ? 1 : 0
      data['use_huanbi_service_fee'] = Number(this.settleInfo.huanbi_service_fee) ? 1 : 0
      data['use_huanbi_income_fee'] = Number(this.settleInfo.huanbi_income_fee) ? 1 : 0
      data['use_huanbi_remit_fee'] = Number(this.settleInfo.huanbi_remit_fee) ? 1 : 0
      data['create_log'] = this.settleInfo.create_log

      confirmSettlement(data).then(
        res => {
          this.$alert(res.info, '', {
            callback: () => {
              this.$root.reload()
            }
          })
        },
        error => {
          this.$alert(error.errorMsg)
        }
      )
    },
    goBack() {
      this.$router.go(-1)
    }
  }
}
</script>

<style lang="scss" scoped>
section {
  background-color: #fff;
  padding: 15px 10px;
  margin-top: 16px;
  h2 {
    margin: 5px 5px;
  }
}
.header {
  margin-bottom: 10px;
}
.radio-wrapper {
  display: inline-block;
  padding: 0 4px;
  border-radius: 4px;
  background-color: #eee;
  margin-left: 10px;
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
.right {
  float: right;
}
.table-head,
.table-wrapper {
  padding: 10px 20px;
}
.table-head {
  margin-top: 10px;
  border: 1px #dcdfe6 solid;
  h1 {
    margin: 10px 0;
  }
}
.table-wrapper {
  border: 1px #dcdfe6 solid;
  .t-center {
    text-align: center;
  }
  .line-item {
    font-size: 15px;
    font-weight: bold;
    label {
      display: inline-block;
      width: 90px;
      font-weight: normal;
    }
  }
  .t-head {
    padding-left: 10px;
    padding-bottom: 20px;
  }
  .t-body {
    width: 100%;
    .t-body-left,
    .t-body-right {
      width: 49%;
      float: left;
    }
    .t-body-left {
      border-right: 1px solid #dcdfe6;
    }
    h2 {
      text-align: center;
    }
    p {
      font-size: 14px;
      display: flex;
      justify-content: space-between;
      padding: 0 20px;
      label {
        font-weight: bold;
      }
    }
  }
}
.t-bottom {
  margin-top: 20px;
  font-size: 14px;
  .t-bottom-left {
    float: left;
    text-indent: 20px;
    width: 150px;
  }
  .t-bottom-right {
    margin-left: 150px;
    span {
      display: inline-block;
      padding: 0 20px;
    }
  }
}
</style>
