<template>
  <div class="container">
    <div class="header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>优惠券</el-breadcrumb-item>
        <el-breadcrumb-item>编辑优惠券</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="list-wrapper" v-loading="newCouponLoading">
      <el-form label-position="center" :model="newCouponData" :rules="rules" ref="newCouponData" label-width="110px">
        <el-form-item label="优惠券名称：" prop="couponName">
          <el-input
            type="text"
            v-model.trim.lazy="newCouponData.couponName"
            placeholder="优惠券名称"
            @input="checkTel('coupon', $event)"
            style="width:30%;"
          ></el-input>
        </el-form-item>
        <el-form-item label="发放总量：" prop="totalCount">
          <el-input
            type="text"
            :disabled="isDisabled"
            v-model.trim.lazy="newCouponData.totalCount"
            placeholder="发放数量"
            @input="checkTel('amount', $event)"
            style="width:30%;"
          ></el-input>
        </el-form-item>
        <el-form-item label="使用门槛：" prop="thresholdType">
          <el-radio-group :disabled="isDisabled" v-model="newCouponData.thresholdType" size="mini">
            <el-radio :label="0">无门槛</el-radio>
            <div style="margin-top: 10px;">
              <el-radio :label="1">订单满</el-radio>
            </div>
          </el-radio-group>
          <el-form-item class="left_flex" prop="thresholdAmount" v-if="newCouponData.thresholdType == 1">
            <el-input
              type="text"
              :disabled="isDisabled"
              v-model.trim.lazy="newCouponData.thresholdAmount"
              placeholder="金额( 0.1-999999.99 )"
              @input="checkTel('reduction', $event)"
              style="width:80%;"
            ></el-input>
            元</el-form-item
          >
        </el-form-item>
        <el-form-item label="优惠内容：" prop="preferentialAmount">
          <span style="margin-right: 10px;">减</span>
          <el-input
            type="text"
            :disabled="isDisabled"
            v-model.trim.lazy="newCouponData.preferentialAmount"
            placeholder="金额(元)"
            @input="checkTel('couponPro', $event)"
            style="width:120px;"
          ></el-input>
          <span style="margin-left: 10px;">元</span>
        </el-form-item>
        <el-form-item label="有效期：" prop="order_valid" required>
          <el-radio-group :disabled="isDisabled" v-model="newCouponData.order_valid" size="mini">
            <div style="margin-bottom: 10px;">
              <el-radio :label="0">选日期</el-radio>
              <el-form-item prop="time_value" v-if="newCouponData.order_valid == 0" style="display: inline-block">
                <el-date-picker
                  :disabled="isDisabled"
                  v-model="time_value"
                  type="datetimerange"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                  @change="optTime"
                ></el-date-picker>
              </el-form-item>
            </div>
            <div class="coup_time">
              <el-radio :label="2">领劵后</el-radio>
              <el-form-item prop="periodType" v-if="newCouponData.order_valid == 2" style="display: inline-block">
                <el-input
                  type="text"
                  :disabled="isDisabled"
                  v-model.trim.lazy="newCouponData.periodType"
                  placeholder="1-999"
                  @input="checkTel('timeDay', $event)"
                  style="width:100px;"
                ></el-input>
              </el-form-item>
            </div>
          </el-radio-group>
          <div
            class="left_flex"
            v-if="newCouponData.order_valid == 2"
            style="vertical-align: bottom;margin-left: 10px;"
          >
            天
          </div>
        </el-form-item>
        <el-form-item label="适用商品：" prop="applicableGoods">
          <template v-if="isDisabled">
            <span style="color: #41a5e1;cursor:pointer" @click=";(dialogTableVisible = true), (goodsType = true)">{{
              addAmount
            }}</span>
          </template>
          <template v-else>
            <el-radio-group v-model="newCouponData.applicableGoods" size="mini">
              <!-- <div style ="margin-bottom: 10px;">
                                <el-radio :label="1">全部商品</el-radio>
                            </div> -->
              <div class="coup_time">
                <el-radio :label="2">指定商品</el-radio>
                <el-button type="primary" size="small" class="input-box">
                  批量导入商品
                  <input ref="referenceUpload" type="file" @change="ossUpload($event)" />
                </el-button>
              </div>
            </el-radio-group>
            <div style="margin-left: 100px;">
              <el-button type="text" size="small" class="input-box">
                <a :href="down">下载模板</a>
              </el-button>
            </div>
            <div class="add-online">
              <el-button type="primary" size="small" @click=";(dialogTableVisible = true), (goodsType = false)"
                >批量添加商品</el-button
              >
              <span class="goods-in">已添加商品：</span>
              <span style="color: #41a5e1;cursor:pointer" @click=";(dialogTableVisible = true), (goodsType = true)">{{
                addAmount
              }}</span>
            </div>
          </template>
        </el-form-item>
        <el-form-item label="领取人：" prop="targetUsers">
          <el-radio-group :disabled="isDisabled" v-model="newCouponData.targetUsers" size="mini">
            <el-radio :label="1">全部</el-radio>
            <el-radio :label="2">新用户</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="领取限制：" prop="order_number">
          <el-radio-group :disabled="isDisabled" v-model="newCouponData.order_number" size="mini">
            <el-radio :label="-1">不限</el-radio>
            <div style="margin-top: 10px;">
              <el-radio :label="2">次数</el-radio>
              <el-form-item prop="receiveLimit" v-if="newCouponData.order_number == 2" style="display: inline-block">
                <el-input
                  type="text"
                  :disabled="isDisabled"
                  v-model.trim.lazy="newCouponData.receiveLimit"
                  placeholder="1-99"
                  @input="checkTel('receive', $event)"
                  style="width:100px;"
                ></el-input>
              </el-form-item>
            </div>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="使用说明：" prop="useDescription">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 4 }"
            placeholder="请输入内容"
            v-model="newCouponData.useDescription"
            maxlength="60"
            style="width: 300px;"
          ></el-input>
        </el-form-item>
        <el-form-item label="备注：">
          <el-input
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 4 }"
            placeholder="请输入内容"
            v-model="newCouponData.remarks"
            maxlength="60"
            style="width: 300px;"
          ></el-input>
        </el-form-item>
        <div style="width: 80%;margin: 0 auto;text-align: center;">
          <el-button style="margin: 0 20px;" @click="resetForm('newCouponData')">取消</el-button>
          <el-button type="primary" style="margin: 0 20px;" @click="submitForm('newCouponData')">确认</el-button>
        </div>
      </el-form>
    </div>
    <el-dialog title="商品列表" :visible.sync="dialogTableVisible" width="90%">
      <goods-online
        :isdisabled="goodsType"
        :couponid="newCouponData.id"
        :operate="isDisabled"
        v-on:add-goods="addGoodsOnline"
        v-if="dialogTableVisible"
      ></goods-online>
    </el-dialog>
  </div>
</template>

<script>
import goodsOnline from './GoodsOnline'
import ROOT from '../../../../api/root.js'
export default {
  name: 'editCoupon',
  components: {
    goodsOnline
  },
  data() {
    let checkDate = (rule, value, callback) => {
      if (this.time_value == null) {
        callback(new Error('请选择有效期限'))
      } else {
        callback()
      }
    }
    return {
      newCoupon: false,
      newCouponData: {
        //新建数据
        id: '',
        couponName: '', //优惠券名称
        totalCount: '', //发放总量
        thresholdAmount: '', //使用门槛
        preferentialAmount: '', //优惠内容
        order_valid: 0, //有效期
        periodType: '', //领券后N天
        periodStartTime: '', //有效期_开始时间
        periodEndTime: '', //有效期_结束时间
        applicableGoods: 2, //适用商品
        targetUsers: 1, //领取人
        receiveLimit: '', //领取次数限制
        useDescription: '', //使用说明
        remarks: '', //备注
        instructions: '0', //优惠叠加
        thresholdType: 0, //门槛类型 0无门槛 1 不包含积分金额 2 包含积分金额
        order_number: 1
      },
      rules: {
        couponName: [
          { required: true, message: '请输入优惠券名称！', trigger: 'blur' },
          { min: 3, max: 15, message: '长度3~15个字符', trigger: 'blur' }
        ],
        totalCount: { required: true, message: '请输入0-99999999整数！', trigger: 'blur' },
        thresholdType: { required: true, message: '请选择使用条件！', trigger: 'change' },
        preferentialAmount: { required: true, message: '请输入0.1-999999.99的数字', trigger: 'blur' },
        time_value: { validator: checkDate, trigger: 'blur' },
        periodType: { required: true, message: '请选择失效天数！', trigger: 'blur' },
        thresholdAmount: { required: true, message: '请填写满减金额！', trigger: 'blur' },
        receiveLimit: { required: true, message: '请填写次数！', trigger: 'blur' },
        applicableGoods: { required: true, message: '请选择适用商品！', trigger: 'change' },
        targetUsers: { required: true, message: '请选择领券人！', trigger: 'change' },
        order_number: { required: true, message: '请选择领取限制！', trigger: 'change' },
        useDescription: { required: true, message: '请填写使用说明！', trigger: 'blur' }
      },
      newCouponLoading: false,
      isShowAdem: true,
      down: '#',
      time_value: null,
      addAmount: 0,
      dialogTableVisible: false,
      goodsType: false,
      isDisabled: false
    }
  },

  methods: {
    addGoodsOnline(message) {
      this.$message.success(message)
      this.dialogTableVisible = false
      this.getAmount()
    },
    getAmount() {
      //获取已添加商品数量
      this.$api.get('hd/itzCouponGoods/page/list', { couponId: this.newCouponData.id, page: 1, limit: 10 }, res => {
        if (res.status == 200) {
          this.addAmount = res.data.total
        }
      })
    },
    getList() {
      this.$api.get(`hd/itzCoupon/info/${this.$route.params.id}`, null, res => {
        if (res.status == 200) {
          this.addAmount = res.data.count
          this.newCouponData.couponName = res.data.coupon.couponName
          this.newCouponData.totalCount = res.data.coupon.totalCount
          this.newCouponData.thresholdType = res.data.coupon.thresholdType
          this.newCouponData.thresholdAmount = res.data.coupon.thresholdType == 1 ? res.data.coupon.thresholdAmount : ''
          this.newCouponData.preferentialAmount = res.data.coupon.preferentialAmount
          this.newCouponData.periodType = res.data.coupon.periodType == 0 ? '' : res.data.coupon.periodType
          this.newCouponData.targetUsers = res.data.coupon.targetUsers
          this.newCouponData.receiveLimit = res.data.coupon.receiveLimit == -1 ? '' : res.data.coupon.receiveLimit
          this.newCouponData.useDescription = res.data.coupon.useDescription
          this.newCouponData.remarks = res.data.coupon.remarks

          this.newCouponData.order_valid = res.data.coupon.periodType == 0 ? 0 : 2
          this.newCouponData.order_number = res.data.coupon.receiveLimit == -1 ? -1 : 2
          this.time_value =
            res.data.coupon.periodType == 0
              ? [res.data.coupon.periodStartTime * 1e3, res.data.coupon.periodEndTime * 1e3]
              : null
          this.optTime()
        }
      })
    },
    /**
     * input输入值验证
     * */
    checkTel(type, e) {
      if (type == 'coupon') {
        this.newCouponData.couponName = this.utils.checkTel('15', e)
      } else if (type == 'amount') {
        this.newCouponData.totalCount = this.utils.checkTel('8', e).replace(/[^\d]+/g, '')
      } else if (type == 'reduction') {
        this.newCouponData.thresholdAmount = this.utils.checkTel('9', e).replace(/^\D*([1-9]\d*\.?\d{0,2})?.*$/, '$1')
      } else if (type == 'couponPro') {
        this.newCouponData.preferentialAmount = this.utils
          .checkTel('9', e)
          .replace(/^\D*([1-9]\d*\.?\d{0,2})?.*$/, '$1')
      } else if (type == 'timeDay') {
        this.newCouponData.periodType = this.utils.checkTel('3', e).replace(/[^\d]+/g, '')
      } else if (type == 'receive') {
        this.newCouponData.receiveLimit = this.utils.checkTel('2', e).replace(/[^\d]+/g, '')
      }
    },
    /**
     * 时间验证
     * */
    optTime() {
      let arr_Time = this.time_value ? this.time_value : []
      if (arr_Time.length > 0) {
        this.newCouponData.periodStartTime = this.utils.format_date(arr_Time[0], '-1') / 1000
        this.newCouponData.periodEndTime = this.utils.format_date(arr_Time[1], '-1') / 1000
      } else {
        this.newCouponData.periodStartTime = ''
        this.newCouponData.periodEndTime = ''
      }
    },
    /**
     * 新建优惠券提交
     *
     * */
    submit(val) {
      if (this.newCouponData.thresholdType == 0) {
        this.newCouponData.thresholdAmount = ''
      }
      if (this.newCouponData.order_valid == 2) {
        this.newCouponData.periodStartTime = parseInt(new Date().getTime() / 1000)
        // this.newCouponData.periodEndTime = this.newCouponData.periodStartTime + 24 * 60 * 60 * this.newCouponData.periodType;
        this.newCouponData.periodEndTime = 0
      } else if (this.newCouponData.order_valid == 0) {
        this.newCouponData.periodType = 0
      }
      if (this.newCouponData.order_number == -1) {
        this.newCouponData.receiveLimit = -1
      }
      this.newCouponLoading = true
      this.$api.put(
        'hd/itzCoupon/' + this.newCouponData.id,
        this.newCouponData,
        res => {
          if (res.status === 200) {
            this.newCoupon = false
            this.newCouponLoading = false
            this.$message.success('修改成功！')
            setTimeout(() => {
              this.$router.push({ name: 'couponList' })
            }, 2000)
          }
        },
        err => {
          this.$message.error(err)
          this.newCouponLoading = false
        }
      )
    },
    /**
     * 验证
     * */
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.newCouponData.applicableGoods == 2) {
            if (this.addAmount == 0) {
              this.$message.error('请添加商品！')
              return false
            }
          }
          this.submit()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    /**
     * 清空验证
     * */
    resetForm(formName) {
      this.newCoupon = false
      this.$refs[formName].resetFields()
      this.$router.push({ name: 'couponList' })
    },
    /**
     * 上传文件
     * @param {object} e 文件
     */
    ossUpload(e) {
      let that = this,
        files = e.target.files
      for (let i of files) {
        if (i.name && i.name.indexOf('.csv') == -1) {
          that.$message.error('请上传 .CSV 文件')
          return false
        }
      }
      that.online_loading = true
      let formData = new FormData()
      formData.append('goodsFile', files[0])
      formData.append('id', this.newCouponData.id)
      that.newCouponLoading = true
      that.$api.post(
        'hd/itzCoupon/goods/csv',
        formData,
        res => {
          if (res.status === 200) {
            that.newCouponLoading = false
            that.$message.success('文件上传成功！')
            that.$refs.referenceUpload.value = null
            that.getAmount()
          }
        },
        err => {
          console.log(err)
          that.newCouponLoading = false
        }
      )
    }
  },
  created() {
    this.isDisabled = this.$route.params.status == 1 ? true : false
    this.newCouponData.id = this.$route.params.id
    this.getList()
    for (let i in ROOT) {
      if (ROOT[i].key == 'hd/') {
        this.down = ROOT[i].val + '/coupon-template(UTF-8).csv'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  font-size: 15px;
  .header {
    margin-bottom: 10px;
  }
  .button-wrapper {
    text-align: right;
    margin-bottom: 10px;
  }
  .search-wrapper {
    padding: 10px;
    margin-bottom: 10px;
    background: #fff;
    border: 1px solid #eee;
    .el-form-item {
      margin-bottom: 0;
    }
  }
  .el-radio {
    margin-right: 10px;
  }
  .left_flex {
    display: inline-block;
    vertical-align: middle;
  }
  .input-box {
    position: relative;
  }
  .input-box input {
    opacity: 0;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 32px;
  }
  .list-wrapper {
    background-color: #fff;
    padding: 10px 40px 30px;
  }
  .add-online {
    position: absolute;
    top: 0;
    left: 220px;
    .goods-in {
      margin-left: 20px;
    }
  }
}
/deep/.el-pagination {
  text-align: center;
  margin-top: 10px;
}
</style>
