<template>
  <div class="container">
    <section>
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item
          >可转账积分来自积分提现账户，这部分积分来自于结算的积分余额。转账积分完成后，无法回到积分提现账户</el-breadcrumb-item
        >
      </el-breadcrumb>
    </section>
    <section class="shop-info">
      <span class="clearfix" style="float: right;color: #409eff;cursor: pointer;margin-top: 10px" @click="goRecord"
        >查看转账记录></span
      >
      <p>
        <span>店铺名称：</span><label>{{ info.shop_name }}</label>
      </p>
      <p>
        <span>积分账户：</span>
        <label>{{ info.remit_account }}</label>
        <span style="font-size: 12px;color:#9c9c9c;margin-left: 10px;">积分余额中结算的积分</span>
      </p>
      <p>
        <span>可转账积分（个）：</span><label>{{ huanbi_remit }}</label>
        <span>可转账积分（个）：</span><label>{{ huanbi_money }}</label>
      </p>
      <p>
        <span>本次转账</span>
        <span>
          <el-input v-model="withSurplus" size="small" maxlength="10"></el-input>
        </span>
        <label @click="getAll" style="color:#409EFF;font-weight: normal;cursor: pointer;">全额转账</label>
      </p>
      <p>
        <span>到账换换注册手机号 ：</span><label>{{ info.bind_user_phone }}</label>
      </p>
      <el-button type="primary" :loading="isLoading" @click="submit" style="width:200px;">提交</el-button>

      <div class="warn-wrapper">
        <p>注：积分提现账户中的积分用途</p>
        <p>1）发放优惠券</p>
        <p>2）转让购买以物抵债商品</p>
      </div>
    </section>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getSuppliersInfo, huanbiWithdraw, getSuppliersAccount } from '../../api/suppliers'
export default {
  name: 'SettlementInfo',
  data() {
    return {
      info: {},
      huanbi_remit: 0, //可提现积分
      huanbi_money: 0, //可提现积分
      withSurplus: '',
      isLoading: false //正在提交
    }
  },
  created() {
    this.getWithdrawalInfo()
  },
  computed: {
    ...mapState({
      suppliers_id: state => state.suppliers.suppliers_id
    })
  },
  methods: {
    getWithdrawalInfo() {
      getSuppliersInfo(this.suppliers_id).then(
        res => {
          this.info = res
        },
        error => {
          console.log(error)
        }
      )
      getSuppliersAccount(this.suppliers_id).then(
        res => {
          this.huanbi_money = this.utils.formatFloat(res.huanbi_money, true)
        },
        error => {
          console.log(error)
        }
      )
    },
    getAll() {
      this.withSurplus = this.huanbi_money
    },
    goRecord() {
      this.$router.push({ name: 'WithdrawalRecord' })
    },
    submit() {
      let withSurplus = Number(this.withSurplus)
      if (!withSurplus || withSurplus < 0) {
        this.$alert('请输入正确的提现金额')
        return
      }
      this.isLoading = true
      huanbiWithdraw(this.suppliers_id, this.withSurplus)
        .then(
          res => {
            this.$alert(res.info, '', {
              callback: () => {
                this.$router.push({ name: 'WithdrawalRecord' })
              }
            })
          },
          error => {
            this.$alert(error.errorMsg)
          }
        )
        .finally(() => {
          this.isLoading = false
        })
    }
  }
}
</script>

<style lang="scss" scoped>
// f8f8f8
section {
  background-color: #fff;
  padding: 15px 25px;
  margin-top: 16px;
  h2 {
    margin: 5px 5px;
  }
}
.header {
  margin-bottom: 10px;
}
.shop-info {
  p {
    font-size: 14px;
    span {
      display: inline-block;
      width: 150px;
      padding-right: 20px;
    }
    label {
      font-weight: bold;
    }
  }
}
.warn-wrapper {
  margin-top: 30px;
  p {
    font-size: 13px;
    margin: 5px 0;
    color: #9c9c9c;
  }
}
</style>
