<template>
  <div class="container">
    <page-title first-title="网贷平台列表"></page-title>
    <div class="button-wrapper">
      <el-button type="primary" @click="activityAdd('add')">添加平台</el-button>
    </div>
    <div class="search-wrapper">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="平台名称">
          <el-input v-model="params.partnerName" placeholder="平台名称"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getActivityList()" icon="el-icon-search">搜索</el-button>
          <el-button type="primary" @click="clear()" icon="el-icon-refresh">清空</el-button>
        </el-form-item>
        <el-form-item style="float:right;">
          <el-button type="primary">
            <a style="color:#fff;" :href="down" download="出借人模板.csv">下载出借人模板</a>
          </el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table
        :data="onlineArr"
        v-loading="online_loading"
        stripe
        style="width: 100%"
        :header-cell-style="{ background: '#eef1f6' }"
      >
        <el-table-column align="center" type="index" label="序号"></el-table-column>
        <el-table-column align="center" prop="partnerId" label="ID"></el-table-column>
        <el-table-column align="center" prop="account" label="账号"></el-table-column>
        <el-table-column align="center" prop="partnerName" label="网贷平台"></el-table-column>
        <el-table-column align="center" prop="mobile" label="手机号"></el-table-column>
        <el-table-column align="center" prop="useToken" label="已代付数额">
          <template slot-scope="scope">
            <router-link :to="{ name: 'paymentDetails', params: { partners: scope.row.partnerName } }"
              ><span>{{ scope.row.useToken }}</span></router-link
            >
          </template>
        </el-table-column>
        <el-table-column align="center" prop="token" label="浣豆余额"></el-table-column>
        <el-table-column align="center" prop="lendNum" label="出借人数">
          <template slot-scope="scope">
            <router-link :to="{ name: 'lenderLists', params: { partners: scope.row.partnerName } }"
              ><span>{{ scope.row.lendNum }}</span></router-link
            >
          </template>
        </el-table-column>
        <el-table-column align="center" prop="createdAt" label="添加时间">
          <template slot-scope="scope">
            <span>{{ utils.format_date(scope.row.createdAt) }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="操作">
          <template slot-scope="scope">
            <el-button type="text" size="small" class="input-box">
              导入出借人
              <input type="file" @change="ossUpload($event, scope.row)" />
            </el-button>
            <el-button @click="editTokenClick(scope.row)" type="text" size="small">增减浣豆</el-button>
            <el-button @click="activityAdd('edit', scope.row)" type="text" size="small">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 时间紧迫，分页暂时不做 -->
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="params.limit"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
    <el-dialog
      :title="titleName"
      :visible.sync="addPlatform"
      v-if="addPlatform"
      width="40%"
      v-loading="addPlatform_loading"
      :close-on-click-modal="false"
    >
      <el-form ref="form" :model="add_Platform" label-width="100px" size="mini">
        <el-form-item label="网贷平台：">
          <el-input type="text" v-model.trim.lazy="add_Platform.partnerName" style="width: 60%;"></el-input>
        </el-form-item>
        <el-form-item label="账号：">
          <el-input
            type="text"
            :disabled="add_Platform.istype == 'edit'"
            v-model.trim.lazy="add_Platform.account"
            style="width: 60%;"
          ></el-input>
        </el-form-item>
        <el-form-item label="初始密码：">
          <el-input type="text" v-model.trim.lazy="add_Platform.password" style="width: 60%;"></el-input>
        </el-form-item>
        <el-form-item label="密码确认：">
          <el-input type="text" v-model.trim.lazy="add_Platform.passwordConfirm" style="width: 60%;"></el-input>
        </el-form-item>
        <!-- <el-form-item label="联系人：">
          <el-input type="text" v-model.trim.lazy="add_Platform.title" style="width: 60%;" ></el-input>
        </el-form-item> -->
        <el-form-item label="手机号：">
          <el-input
            type="number"
            v-model.trim.lazy="add_Platform.mobile"
            @input="checkTel('phone', $event)"
            style="width: 60%;"
          ></el-input>
        </el-form-item>
        <el-form-item label="赠送浣豆：" v-if="add_Platform.istype != 'edit'">
          <el-input
            type="number"
            v-model.trim.lazy="add_Platform.token"
            @input="checkTel('token', $event)"
            style="width: 60%;"
          ></el-input>
        </el-form-item>
        <div class="antion-footer">
          <el-button @click="addPlatform = false">取 消</el-button>
          <el-button type="primary" @click="submit" :loading="addBtn">确 定</el-button>
        </div>
      </el-form>
    </el-dialog>
    <el-dialog title="修改浣豆" :visible.sync="kidneyBean" v-if="kidneyBean" width="30%" :close-on-click-modal="false">
      <el-form ref="form" :model="editToken" label-width="100px" size="mini">
        <el-form-item label="浣豆：">
          <el-input
            type="number"
            v-model.trim.lazy="editToken.token"
            placeholder="输入负数为减少浣豆"
            maxlength="8"
            @input="checkTel('token', $event)"
            style="width: 100%;"
          ></el-input>
        </el-form-item>
        <div class="antion-footer">
          <el-button @click="kidneyBean = false">取 消</el-button>
          <el-button type="primary" @click="increaseOrDecrease" :loading="editBtn">确 定</el-button>
        </div>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import ROOT from '../../../api/root.js'
export default {
  name: 'RetailActivityList',
  data() {
    return {
      onlineArr: [],
      currentPage: 1,
      params: {
        partnerName: '', //网贷平台名称
        page: 1,
        limit: 10
      },
      add_Platform: {
        partnerName: '', //合作平台名称
        token: '', //浣豆
        account: '', //平台账号
        password: '', //密码
        passwordConfirm: '', //重复密码
        mobile: '', //手机号
        partnerId: '' //住建ID
      },
      total: 0,
      addPlatform_loading: false,
      addPlatform: false,
      kidneyBean: false,
      addBtn: false,
      editBtn: false,
      editToken: {
        token: '',
        partnerId: ''
      },
      fileList3: [],
      titleName: '',
      online_loading: false,
      down: ''
    }
  },
  created() {
    this.getActivityList()
    for (let i in ROOT) {
      if (ROOT[i].key == 'hd/') {
        this.down = ROOT[i].val + '/template(UTF-8).csv'
      }
    }
  },
  methods: {
    checkTel(type, e) {
      if (type == 'phone') {
        if (this.add_Platform.mobile.length > 11) {
          this.add_Platform.mobile = this.add_Platform.mobile.slice(0, 11)
        }
      } else if (type == 'token') {
        if (e == 0) {
          this.editToken.token = ''
        }
        if (this.editToken.token.length > 8) {
          this.editToken.token = this.editToken.token.slice(0, 8)
        }
      }
    },
    replaceDatas(data) {
      for (let key in data) {
        if (data[key] == '' || data[key] == null) {
          delete data[key]
        }
      }
    },
    getActivityList() {
      this.replaceDatas(this.params)
      let pramstr = JSON.stringify(this.params)
        .replace(/:/g, '=')
        .replace('{', '')
        .replace('}', '')
        .replace(/,/g, '&')
        .replace(/"/g, '')
        .replace(/\s+/g, '')
      this.$api.get('hd/itzPartner/page/list?' + pramstr, null, res => {
        if (res.status === 200) {
          this.onlineArr = res.data.rows
          this.$set(this.onlineArr, res.data.rows)
          this.total = res.data.total
        }
      })
    },
    handleCurrentChange(currentPage) {
      this.params.page = currentPage
      this.getActivityList()
    },
    /**
     * 合作平台名称 private String partnerName;
     * 浣豆 private BigDecimal token;
     * 创建时间 private Integer createdAt;
     * 更新时间 private Integer updatedAt;
     * 平台名称 private String account;
     * 密码 private String password;
     * 重复密码 private String passwordConfirm;
     * 手机号 private String mobile;
     */
    activityAdd(type, n) {
      console.log(n)
      if (type == 'add') {
        this.add_Platform = {
          istype: 'add',
          partnerName: '', //合作平台名称
          token: '', //浣豆
          account: '', //平台账号
          password: '', //密码
          passwordConfirm: '', //重复密码
          mobile: '', //手机号
          partnerId: '' //住建ID
        }
        this.titleName = '添加平台'
      } else if (type == 'edit') {
        this.add_Platform = {
          istype: 'edit',
          partnerName: n.partnerName, //合作平台名称
          token: n.token, //浣豆
          account: n.account, //平台账号
          password: '', //密码
          passwordConfirm: '', //重复密码
          mobile: n.mobile, //手机号
          partnerId: n.partnerId //住建ID
        }
        this.titleName = '修改平台'
      }
      this.addPlatform = true
    },
    // 清空搜索框
    clear() {
      // console.log(Object.values(this.params));
      this.page = 1
      this.limit = 10
      let _params = this.params
      _params.partnerName = ''
      this.getActivityList()
    },
    /**
     * 导入出借人
     */
    ossUpload(e, n) {
      console.log(e.target.files, n)
      let that = this,
        files = e.target.files
      for (let i of files) {
        if (i.name && !i.name.indexOf('.csv') == -1) {
          that.$message.error('.CSV 文件')
          continue
        }
      }
      that.online_loading = true
      let formData = new FormData()
      formData.append('file', files[0])
      that.$api.post('hd/upload/' + n.partnerId, formData, res => {
        console.log(res)
        if (res.status === 200) {
          that.online_loading = false
          that.$message.success('文件上传成功！')
          this.getActivityList()
        }
      })
    },
    editTokenClick(e) {
      console.log(e)
      this.kidneyBean = true
      this.editToken = {
        token: '',
        partnerId: e.partnerId
      }
    },
    /**
     * 增减浣豆
     * @param {obj} pramstr
     * token:'',浣豆
     * partnerId:'' 主键编码
     */
    increaseOrDecrease() {
      this.editBtn = true
      let pramstr = this.editToken
      if (!this.editToken.token) {
        this.$message.error(`浣豆数量不能为空！`)
        return false
      }
      if (this.editToken.token && this.editToken.token.length > 8) {
        this.$message.error(`浣豆最大数量支持八位数！`)
        return false
      }
      this.$api.put('hd/itzPartner/huandou', pramstr, res => {
        console.log(res)
        if (res.status === 200) {
          this.$message.success(`新增成功`)
          this.editBtn = false
          this.kidneyBean = false
          this.getActivityList()
        }
      })
    },
    /**
     * 添加网贷平台
     * @param {obj} add_Platform
     * add_Platform.partnerName:'', //合作平台名称
     * add_Platform.token:'',//浣豆
     * add_Platform.account :'', //平台账号
     * add_Platform.password :'', //密码
     * add_Platform.passwordConfirm:'', //重复密码
     * add_Platform.mobile:'', //手机号
     * add_Platform.partnerId:''//住建ID
     */
    submit() {
      if (this.add_Platform.istype == 'add') {
        if (!this.add_Platform.partnerName) {
          this.$message.error(`合作平台名称不能为空！`)
          return false
        }
        if (!this.add_Platform.account) {
          this.$message.error(`合作账号不能为空！`)
          return false
        }
        if (!this.add_Platform.account) {
          this.$message.error(`平台账号不能为空！`)
          return false
        }
        if (!this.add_Platform.password) {
          this.$message.error(`密码不能为空！`)
          return false
        }
        if (this.add_Platform.password != this.add_Platform.passwordConfirm) {
          this.$message.error(`两次输入的密码不一致！`)
          return false
        }
        let telrule = /^1[3456789]\d{9}$/
        if (!this.add_Platform.mobile) {
          this.$message.error(`请输入手机号！`)
          return false
        }
        if (!telrule.test(this.add_Platform.mobile)) {
          this.$message.error(`手机号格式不正确！`)
          return false
        }
        if (!this.add_Platform.token) {
          this.$message.error(`浣豆数量不能为空！`)
          return false
        }
        if (this.add_Platform.token && this.add_Platform.token.length > 8) {
          this.$message.error(`浣豆最大数量支持八位数！`)
          return false
        }
        let pramstr = this.add_Platform
        this.addBtn = true
        this.$api.post('hd/itzPartner/info', pramstr, res => {
          console.log(res)
          if (res.status === 200) {
            this.$message.success(`新增成功`)
            this.addBtn = false
            this.addPlatform = false
            this.getActivityList()
          }
        })
      } else if (this.add_Platform.istype == 'edit') {
        this.addBtn = true
        let pramstr = this.add_Platform
        this.$api.put('hd/itzPartner/info', pramstr, res => {
          if (res.status === 200) {
            this.$message.success(`修改完成！`)
            this.addBtn = false
            this.addPlatform = false
            this.getActivityList()
          }
        })
      }
    }
  },
  watch: {
    addPlatform(val) {
      let that = this
      if (val === false || val === 'false') {
        console.log('重置add_Platform')
        that.add_Platform = {
          istype: '',
          partnerName: '', //合作平台名称
          token: '', //浣豆
          account: '', //平台账号
          password: '', //密码
          passwordConfirm: '', //重复密码
          mobile: '', //手机号
          partnerId: '' //住建ID
        }
        that.addBtn = false
      }
    },
    kidneyBean(val) {
      let that = this
      if (val === false || val === 'false') {
        console.log('修改浣豆')
        that.editToken = {
          token: '',
          partnerId: ''
        }
        that.editBtn = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.button-wrapper {
  // text-align: right;
  padding: 15px 20px;
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
.antion-footer {
  text-align: center;
  margin: 30px 0 0;
}
.el-input--mini .el-input__inner {
  height: 32px;
  line-height: 32px;
}
.input-box {
  position: relative;
}
.input-box input {
  opacity: 0;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 50px;
}
</style>
