<template>
  <div class="container">
    <page-title first-title="下单有礼" second-title="新建活动"></page-title>
    <div class="wrapper">
      <el-form
        label-position="left"
        :model="newAcitveData"
        :rules="rules"
        ref="newAcitveData"
        label-width="140px"
        style="width:600px"
      >
        <el-form-item label="活动名称" prop="cashName">
          <el-input maxlength="20" v-model="newAcitveData.cashName"></el-input>
        </el-form-item>
        <el-form-item label="活动时间" required>
          <el-col :span="11">
            <el-form-item prop="startTime">
              <el-date-picker
                type="datetime"
                placeholder="年-月-日 时:分"
                v-model="newAcitveData.startTime"
                format="yyyy-MM-dd HH:mm"
                value-format="timestamp"
                style="width: 100%;"
                :picker-options="pickerOptions0"
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="2" style="text-align: center;">-</el-col>
          <el-col :span="11">
            <el-form-item prop="endTime">
              <el-date-picker
                type="datetime"
                value-format="timestamp"
                placeholder="年-月-日 时:分"
                v-model="newAcitveData.endTime"
                format="yyyy-MM-dd HH:mm"
                style="width: 100%;"
                :picker-options="pickerOptions1"
                default-time="23:59:59"
                @change="formatendtime"
              ></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="活动类型" prop="cashType" class="cashType">
          <el-row><span>现金红包</span></el-row>
          <el-radio-group v-model="newAcitveData.cashType" @change="AmonutCach">
            <el-row>
              <el-col :span="10">
                <el-radio label="1">固定金额 </el-radio>
              </el-col>
              <el-col :span="10">
                <el-radio label="2">订单金额百分比 </el-radio>
              </el-col>
            </el-row>
            <el-form-item class="radioBox" v-if="!Amountflag">
              <input
                type="number"
                min="1"
                step="1"
                @blur="checks"
                :class="{ active: iswarn }"
                v-model.number="newAcitveData.cashAmount"
              />
              <span> 元</span>
              <p id="warn_red"></p>
            </el-form-item>
            <el-form-item class="radioBox" v-else>
              <input
                type="number"
                min="0.01"
                max="100"
                step="0.01"
                @blur="checks"
                :class="{ active: iswarn0 }"
                v-model.number="newAcitveData.cashAmount0"
              />
              <span> %</span>
              <span class="warn">‘订单金额百分比’为支付现金金额的百分比</span>
              <p id="warn_red"></p>
            </el-form-item>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="单次最高返现金额" prop="maxSingleCash" v-show="Amountflag">
          <el-input v-model.number="newAcitveData.maxSingleCash"></el-input>
        </el-form-item>
        <el-form-item label="活动总额" prop="totalAmount">
          <el-input type="number" min="1" max="99999999" maxlength="8" v-model.number="newAcitveData.totalAmount">
            {{ utils.formatMoney(newAcitveData.totalAmount) }}
          </el-input>
          <span class="warn">输入 -1 代表不限制活动金额</span>
        </el-form-item>
        <el-form-item label="活动人群" prop="targetUsers">
          <el-radio-group v-model="newAcitveData.targetUsers">
            <el-radio label="2">全部</el-radio>
            <el-radio label="1">新用户</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="有礼单数">
          <el-input v-model.number="newAcitveData.maxNumber" disabled></el-input>
          <span class="warn">活动开始后可参加下单有礼的订单数</span>
        </el-form-item>
        <el-form-item label="活动商品" prop="cashScope">
          <el-radio-group v-model="newAcitveData.cashScope">
            <el-radio label="0">全部商品</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="优先级" prop="priority">
          <el-input type="number" min="1" max="999" maxlength="3" v-model.number="newAcitveData.priority"></el-input>
          <span class="warn">请输入1-999数字。字数越大优先级越高。优先级相同的活动，以便及时间为准</span>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="check('newAcitveData', newAcitveData)">确定</el-button>
          <el-button @click="back">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    var checkAmount = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('活动总额不能为空或者0'))
      }

      if (-2 > value || value > 99999999) {
        callback(new Error('总金额取值区间为1-99999999'))
      } else {
        callback()
      }
    }
    return {
      subm: true,
      newAcitveData: {
        cashName: '',
        startTime: Date.now() + 3600 * 1000 * 1,
        endTime: '',
        cashAmount: '',
        cashAmount0: '',
        cashType: '1',
        targetUsers: '1',
        totalAmount: '-1',
        maxNumber: '1',
        cashScope: '0',
        priority: '',
        maxSingleCash: ''
      },
      iswarn: false,
      iswarn0: false,
      AcitveData: {},
      pickerOptions0: {
        disabledDate: time => {
          if (this.newAcitveData.endTime != '' && this.newAcitveData.endTime != null) {
            return time.getTime() < Date.now() - 3600 * 1000 * 24 * 1 || time > this.newAcitveData.endTime
          } else {
            return time.getTime() < Date.now() - 3600 * 1000 * 24 * 1
          }
        }
      },
      pickerOptions1: {
        disabledDate: time => {
          return time < this.newAcitveData.startTime - 3600 * 1000 * 24 * 1
        }
      },
      Amountflag: false,
      rules: {
        cashName: [
          { required: true, message: '请输入活动名称', trigger: 'blur' },
          { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
        ],
        startTime: [{ type: 'date', required: true, message: '请选择开始时间', trigger: 'change' }],
        endTime: [{ type: 'date', required: true, message: '请选择结束时间', trigger: 'change' }],
        totalAmount: [{ required: true, validator: checkAmount, trigger: 'blur' }],
        targetUsers: [{ required: true, message: '请输入活动名称', trigger: 'blur' }],
        priority: [
          { required: true, message: '优先级不能为空', trigger: 'blur' },
          { type: 'number', min: 1, max: 999, message: '优先级在1-999之间', trigger: 'blur' }
        ],
        cashType: [{ required: true, message: '请选择活动类型', trigger: 'change' }],
        maxSingleCash: [
          { required: true, type: 'number', message: '单次最高返现金额不能为空', trigger: 'blur' },
          { type: 'number', min: 1, max: 99999999, message: '订单金额区间为1-99999999', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    check(str, pramas) {
      if (pramas.cashType == 1) {
        pramas.maxSingleCash = Number(pramas.cashAmount)
      } else {
        pramas.cashAmount = pramas.cashAmount0
      }
      this.$refs[str].validate(valid => {
        if (valid && this.subm) {
          this.subm = false
          pramas.startTime = parseInt(pramas.startTime / 1000)
          pramas.endTime = parseInt(pramas.endTime / 1000)
          this.$api.post(
            `/hd/itzReturnCashActivity/check`,
            pramas,
            r => {
              if (r.status === 200) {
                this.submit(pramas) //新增提交
              } else if (r.status === 30101) {
                this.subm = true
                // console.log(pramas)
                pramas.startTime = pramas.startTime * 1000
                pramas.endTime = pramas.endTime * 1000
                this.$alert(r.message, '提示', {
                  confirmButtonText: '确定',
                  callback: () => {
                    pramas.cashName = ''
                  }
                })
                return
              } else if (r.status === 30102) {
                pramas.startTime = pramas.startTime * 1000
                pramas.endTime = pramas.endTime * 1000
                this.subm = true
                this.$confirm('该时间段已有返现活动，是否继续创建?', '提示', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type: 'warning'
                })
                  .then(() => {
                    pramas.startTime = parseInt(pramas.startTime / 1000)
                    pramas.endTime = parseInt(pramas.endTime / 1000)
                    this.submit(pramas) //新增提交
                  })
                  .catch(() => {
                    this.$message({
                      type: 'info',
                      message: '已取消新增'
                    })
                  })
              } else {
                this.subm = true
                pramas.startTime = pramas.startTime * 1000
                pramas.endTime = pramas.endTime * 1000
              }
            },
            err => {
              console.log(err)
              this.subm = true
              pramas.startTime = pramas.startTime * 1000
              pramas.endTime = pramas.endTime * 1000
            }
          )
        } else {
          this.checks()
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    submit(pramas) {
      this.$refs['newAcitveData'].validate(valid => {
        if (valid) {
          this.$api.post(`/hd/itzReturnCashActivity`, pramas, r => {
            if (r.status === 200) {
              this.$message('添加成功')
              this.back()
            } else {
              this.$message.error({
                title: '错误',
                message: r.message
              })
              pramas.startTime = pramas.startTime * 1000
              pramas.endTime = pramas.endTime * 1000
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    AmonutCach(label) {
      console.log(this.Amountflag)
      this.subm = false
      label == '1' ? (this.Amountflag = false) : (this.Amountflag = true)
      this.checks()
    },
    formatendtime(value) {
      if (value) {
        if (value < this.newAcitveData.startTime) {
          this.$alert('结束时间不能早于开始时间', '提示', {
            confirmButtonText: '确定',
            callback: () => {
              this.newAcitveData.endTime = ''
            }
          })
          return
        }
      }
    },
    back() {
      this.$router.go(-1)
    },
    checks() {
      //订单金额百分比验证
      console.log('check', this.Amountflag)
      var val = ''
      this.subm = false
      this.iswarn = true
      this.iswarn0 = true
      if (this.Amountflag) {
        document.getElementById('warn_red').innerHTML = ''
        val = this.newAcitveData.cashAmount0

        console.log('val', val)
        if (val == '') {
          document.getElementById('warn_red').innerHTML = '订单金额百分比不能为空'
        } else if (val < 0.01 || val > 100) {
          document.getElementById('warn_red').innerHTML = '订单金额百分比取值区间为0.01-100'
        } else {
          document.getElementById('warn_red').innerHTML = ''
          this.subm = true
          this.iswarn0 = false
        }
      } else {
        document.getElementById('warn_red').innerHTML = ''
        val = this.newAcitveData.cashAmount
        console.log('val', val)
        if (val == '') {
          document.getElementById('warn_red').innerHTML = '红包金额不能为空'
        } else if (val < 1 || val > 99999999) {
          document.getElementById('warn_red').innerHTML = '红包金额取值区间为1-99999999'
        } else {
          document.getElementById('warn_red').innerHTML = ''
          this.subm = true
          this.iswarn = false
        }
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.wrapper {
  padding: 30px;
  margin-top: 10px;
  background: #fff;
  border: 1px solid #eee;

  .block {
    display: block;
  }
  .warn {
    display: block;
    line-height: 1em;
    font-size: 12px;
    padding-top: 4px;
    color: #999;
  }
  .cashType {
    .el-radio-group {
      display: block;
      .radioBox {
        margin-top: 10px;
        input {
          width: 220px;
          background-color: #fff;
          border-radius: 4px;
          border: 1px solid #dcdfe6;
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          color: #606266;
          display: inline-block;
          font-size: inherit;
          height: 40px;
          line-height: 40px;
          outline: 0;
          padding-left: 15px;
          &.active {
            border: 1px solid #f56c6c;
          }
        }
        .el-input {
          width: 220px;
        }
        #warn_red {
          color: #f56c6c;
          font-size: 12px;
          line-height: 1;
          padding-top: 4px;
          position: absolute;
          top: 100%;
          left: 0;
          margin: 0;
        }
      }
    }
  }
  .el-row {
    width: 100%;
  }
}
</style>
