<template>
  <div class="wait-confirm-detail-container">
    <!-- header -->
    <page-title first-title="对账单列表" second-title="详情"></page-title>

    <!-- total -->
    <div class="total-wrapper">
      <div class="account-info">
        <div class="account-left">状态：{{ fmtStatus(infoData.status) }}</div>
        <div class="account-right">
          <span>对账单编号：</span>
          <span>{{ infoData.multi_statement_id }}</span>
          <span class="settle-time">结算时间：</span>
          <span
            >{{ (infoData.settlement_start * 1e3) | convertTime }} ——
            {{ (infoData.settlement_end * 1e3) | convertTime }}</span
          >
        </div>
      </div>
      <v-total :info="infoData" v-if="infoData.multi_statement_id"></v-total>
      <div class="btn-box" v-if="infoData.status && infoData.status != 4">
        <!-- 当为待运营确认时, 有编辑按钮-->
        <el-button type="warning" @click="goEdit" v-if="isOperater">重新编辑</el-button>
        <el-button type="warning" @click="hasDiff" v-if="isSuppliers || isFinance">有异议</el-button>
        <el-button type="primary" @click="confirm" v-if="isOperater || isSuppliers || isFinance">确认</el-button>
      </div>
    </div>

    <div class="bill-detail-wrapper">
      <div class="account-info">
        <div class="account-right">
          <span class="title">账单明细</span>
          <span>日账单编号：</span>
          <span v-if="infoData.statement_no">{{ infoData.statement_no.join(', ') }}</span>
        </div>
      </div>
      <div class="btn-box">
        <el-button type="primary" size="small" @click="exportSettlement">导出账单</el-button>
      </div>
      <el-tabs @tab-click="changeTab" v-model="params.type">
        <!-- 收入 -->
        <el-tab-pane label="订单总收入" name="1">
          <!-- <v-income></v-income> -->
          <el-table :data="listData" border stripe :header-cell-style="{ background: '#eef1f6' }">
            <el-table-column prop="order_sn" label="订单号"></el-table-column>
            <el-table-column prop="add_time" label="下单时间" :formatter="fmtTime"></el-table-column>

            <el-table-column label="支付金额（元）">
              <template v-slot="scope">
                <span v-if="scope.row.order_type == 1">{{ scope.row.total_amount }}</span>
                <span v-else>{{ scope.row.goods_amount }}</span>
              </template>
            </el-table-column>
            <el-table-column label="支付现金额（元）">
              <template v-slot="scope">
                <span v-if="scope.row.order_type == 1">{{ scope.row.real_money_paid }}</span>
                <span v-else>{{ scope.row.money_paid }}</span>
              </template>
            </el-table-column>
            <el-table-column label="支付积分额" v-if="cooperateType == 1">
              <template v-slot="scope">
                <span v-if="scope.row.order_type == 1">{{ scope.row.real_surplus }}</span>
                <span v-else>{{ scope.row.surplus }}</span>
              </template>
            </el-table-column>
            <el-table-column prop="real_cash_back" label="现金退款"></el-table-column>
            <el-table-column prop="real_surplus_back" label="积分退款" v-if="cooperateType == 1"></el-table-column>
            <el-table-column prop="shipping_fee" label="运费"></el-table-column>
            <el-table-column prop="showStatus" label="订单状态"></el-table-column>
            <el-table-column label="操作">
              <template v-slot="scope">
                <el-button @click="goDetail(scope.row.order_id)" type="text" size="small">查看</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>

        <!-- 支出 -->
        <el-tab-pane label="订单总支出" name="2">
          <!-- <v-expend></v-expend> -->
          <el-table :data="listData" border stripe :header-cell-style="{ background: '#eef1f6' }">
            <el-table-column prop="order_sn" label="订单号"></el-table-column>
            <el-table-column prop="add_time" label="下单时间" :formatter="fmtTime"></el-table-column>
            <el-table-column prop="totalBack" label="支出金额（元）"></el-table-column>
            <el-table-column prop="cash_back" label="支出现金额（元）"></el-table-column>
            <el-table-column prop="surplus_back" label="支出积分额" v-if="cooperateType == 1"></el-table-column>
            <el-table-column prop="showStatus" label="订单状态"></el-table-column>
            <el-table-column label="操作">
              <template v-slot="scope">
                <el-button @click="goDetail(scope.row.order_id)" type="text" size="small">查看</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page.sync="params.page"
        layout="prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>

    <!-- operate -->
    <div class="operate-wrapper">
      <a @click="switchOperate">操作详情</a>
      <v-operate v-if="showOperate" :actionlist="actionList"></v-operate>
    </div>

    <!-- 异议弹窗 -->
    <div class="different-idea">
      <el-dialog
        :visible.sync="showDialog"
        :title="isFinance && !isFinanceHasDiff ? '请填写备注' : '提交后您的对账单将退回至运营'"
      >
        <div class="idea-content">
          <span style="color: red; margin-right: 10px;">*</span>
          <el-input type="textarea" v-model="remark" placeholder="请填写备注，100字以内"></el-input>
        </div>
        <div class="btn-box">
          <el-button type="primary" size="small" @click="submitDiff">提交</el-button>
          <el-button type="default" size="small" @click="showDialog = false">取消</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import {
  getStatementInfo,
  getSettlementDetailList,
  exportAccountList,
  hasDifferentIdea,
  financeHasDiff,
  operaterConfirm,
  financeConfirm,
  confirmSettlement,
  getAllAction
} from '../../api/settle'
import UnsettledDetailTotal from './child/UnsettledDetailTotal'
import OperateRecord from './child/OperateRecord'
import OrderIncome from './child/OrderIncome'
import { saveAs } from 'file-saver'
import { mapState } from 'vuex'
import { SETTLE_ACCOUNT_STATUS } from './static'
export default {
  name: '',
  data() {
    return {
      params: {
        multi_statement_id: this.$route.query.id || '',
        type: '1',
        page: 1,
        size: 10
      },
      total: 0,
      infoData: {},
      incomeData: [],
      expendData: [],
      listData: [],
      actionList: [],
      showOperate: false,
      showDialog: false,
      remark: '', // 异议
      statusList: SETTLE_ACCOUNT_STATUS,
      isFinanceHasDiff: false
    }
  },
  components: {
    'v-total': UnsettledDetailTotal,
    'v-operate': OperateRecord,
    'v-income': OrderIncome
  },
  computed: {
    ...mapState({
      role: state => state.settle.role,
      cooperateType: state => state.suppliers.cooperateType,
      suppliers_id: state => state.suppliers.suppliers_id
    }),
    /**
     * 当前状态为非完成，且对应待确认角色与当前登录角色一致时，显示按钮，all可以操作一切
     */
    // 待运营确认
    isOperater() {
      return (this.role == 'all' || this.role == 'operater') && this.infoData.status == 1
    },
    // 待商家确认
    isSuppliers() {
      return (this.role == 'all' || this.role == 'supplier') && this.infoData.status == 2
    },
    // 待财务确认
    isFinance() {
      return (this.role == 'all' || this.role == 'finance') && this.infoData.status == 3
    }
  },
  created() {
    this.getInfo()
    this.getList()
    this.getAction()
  },
  methods: {
    getInfo() {
      getStatementInfo(this.params.multi_statement_id).then(res => {
        this.infoData = res || {}
      })
    },
    getList() {
      getSettlementDetailList(this.params).then(res => {
        this.total = res.paged.total
        this.listData = res.list
      })
    },
    getAction() {
      getAllAction({ multi_statement_id: this.params.multi_statement_id }).then(res => {
        this.actionList = res || []
      })
    },
    switchOperate() {
      this.showOperate = !this.showOperate
    },
    goEdit() {
      this.$router.push({
        name: 'SettlementAdd',
        params: { id: this.params.multi_statement_id },
        query: { isedit: true }
      })
    },
    goDetail(id) {
      window.location.href = '/admin/order.php?act=info&order_id=' + id
    },
    exportSettlement() {
      exportAccountList({
        suppliers_id: this.suppliers_id,
        multi_statement_id: this.params.multi_statement_id
      }).then(
        res => {
          const date = this.utils.formatDate('YYYY-MM-DD', new Date().getTime())
          saveAs(new Blob([res.data]), res.filename || date + '结算单.xls')
        },
        error => {
          console.log(error)
        }
      )
    },
    changeTab() {
      this.getList()
    },
    handleCurrentChange() {
      this.getList()
    },
    fmtTime(row, column, cellValue) {
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', cellValue * 1e3)
    },
    submitDiff() {
      if (!this.remark) {
        this.$message.error('请填写原因')
        return
      }
      if (this.isFinance && !this.isFinanceHasDiff) {
        financeConfirm({ multi_statement_id: this.params.multi_statement_id, remit_log: this.remark }).then(
          res => {
            this.$message.success('确认成功')
            this.showDialog = false
            this.isFinanceHasDiff = false
            this.getInfo()
            this.getAction()
          },
          err => {
            this.$message.error(err.errorMsg)
          }
        )
      } else if (this.isFinance && this.isFinanceHasDiff) {
        financeHasDiff({ multi_statement_id: this.params.multi_statement_id, remark: this.remark }).then(
          res => {
            this.$message.success('已退回至运营')
            this.showDialog = false
            this.getInfo()
            this.getAction()
          },
          err => {
            this.$message.error(err.errorMsg)
          }
        )
      } else {
        hasDifferentIdea({ multi_statement_id: this.params.multi_statement_id, remark: this.remark }).then(
          res => {
            this.$message.success('已退回至运营')
            this.showDialog = false
            this.getInfo()
            this.getAction()
          },
          err => {
            this.$message.error(err.errorMsg)
          }
        )
      }
    },
    hasDiff() {
      this.showDialog = true
      if (this.isFinance) {
        this.isFinanceHasDiff = true
      }
    },
    confirm() {
      let p = { multi_statement_id: this.params.multi_statement_id }
      if (this.isOperater) {
        operaterConfirm(p).then(res => {
          this.$message.success('确认成功')
          this.getInfo()
          this.getAction()
        })
      } else if (this.isFinance) {
        this.showDialog = true
        this.isFinanceHasDiff = false
      } else {
        confirmSettlement(p).then(res => {
          this.$message.success('确认成功')
          this.getInfo()
          this.getAction()
        })
      }
    },
    fmtStatus(s) {
      let _n = ''
      this.statusList.forEach(item => {
        if (item.value == s) {
          _n = item.name
        }
      })
      return _n
    }
  }
}
</script>

<style lang="scss" scoped>
.wait-confirm-detail-container {
  .btn-box {
    text-align: right;
    margin-top: 20px;
  }
  /deep/ .el-table {
    margin-top: 20px;
  }
  .operate-wrapper {
    margin-top: 20px;
  }
  a {
    cursor: pointer;
  }
  .account-info {
    background: #fff;
    padding: 15px;
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    .account-right {
      display: flex;
      align-items: center;
      .title {
        font-size: 16px;
        font-weight: bold;
        margin-right: 30px;
      }
      .settle-time {
        margin-left: 50px;
      }
    }
    .account-left {
      font-size: 20px;
      font-weight: bold;
    }
  }
  .idea-content {
    display: flex;
  }
  .el-pagination {
    text-align: center;
    margin-top: 15px;
  }
}
</style>
