<template>
  <div class="container">
    <page-title first-title="下单有礼"></page-title>
    <div class="search-wrapper">
      <el-form :model="params" size="small" label-position="left" label-width="100px">
        <el-row :gutter="20">
          <el-col :span="11">
            <el-form-item label="活动名称">
              <el-input v-model="params.cashName" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="活动类型">
              <el-select v-model="params.cashType" placeholder="活动类型">
                <el-option label="现金红包" value="1"></el-option>
                <el-option label="优惠券" value="2" disabled></el-option>
              </el-select>
              <!-- <el-select v-model="params.type">
            <el-option
              v-for=" value in typeList"
              :key="value.id"
              :label="value.name"
              :value="value.name"
            ></el-option>
          </el-select> -->
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="11">
            <el-form-item label="活动日期">
              <el-date-picker
                v-model="timeinterval"
                value-format="timestamp"
                type="daterange"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :default-time="['00:00:00', '23:59:59']"
                style="width:100%;"
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="活动状态">
              <el-select v-model="params.cashStatus">
                <el-option
                  v-for="value in statusList"
                  :key="value.id"
                  :label="value.name"
                  :value="value.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
          <el-button @click="clear">清空</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="button-wrapper">
      <router-link :to="{ name: 'newActivity' }">
        <el-button type="primary" size="small">新增活动</el-button>
      </router-link>
    </div>
    <div class="list-wrapper">
      <el-table :data="list" style="width: 100%">
        <el-table-column type="index" label="序号"></el-table-column>
        <el-table-column prop="cashName" label="活动名称"></el-table-column>
        <el-table-column prop="startTime" label="活动开始时间" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="endTime" label="活动结束时间" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="cashType" label="活动类型" :formatter="typeFormat"></el-table-column>
        <el-table-column label="活动金额">
          <template slot-scope="scope">
            <span v-if="scope.row.cashType == 1"> {{ scope.row.cashAmount }}元</span>
            <span v-else> {{ scope.row.cashAmount }}%</span>
          </template>
        </el-table-column>
        <el-table-column prop="priority" label="优先级"></el-table-column>
        <el-table-column prop="status" label="活动状态" :formatter="statusType"></el-table-column>
        <el-table-column prop="createTime" label="创建时间" :formatter="dateFormat"></el-table-column>

        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button @click="ShowAcitves(scope.row.id)" type="text" size="small">查看</el-button>
            <template v-if="scope.row.status === 0">
              <el-button @click="EditAcitves(scope.row.id)" type="text" size="small">编辑</el-button>
            </template>
            <template v-if="scope.row.status != 0">
              <el-button @click="viewDetail(scope.row.id)" type="text" size="small">活动统计</el-button>
            </template>
            <template v-if="scope.row.status === 1">
              <el-button @click="stopAct(scope.row.id)" type="text" size="small">手动结束</el-button>
            </template>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="params.limit"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
    <!-- 查看 -->
    <el-dialog title="查看活动" :visible.sync="showAcitve" width="40%" :close-on-click-modal="false">
      <el-form label-position="left" v-model="AcitveData" size="small" label-width="140px">
        <el-form-item label="活动名称：">
          <span>{{ AcitveData.cashName }}</span>
        </el-form-item>
        <el-form-item label="活动开始时间：">
          <span>{{ dateFormat0(AcitveData.startTime) }}</span>
        </el-form-item>
        <el-form-item label="活动结束时间：">
          <span>{{ dateFormat0(AcitveData.endTime) }}</span>
        </el-form-item>
        <el-form-item label="活动类型：">
          <span class="block">现金红包</span>
          <span v-if="AcitveData.cashType == 1">固定金额 {{ AcitveData.cashAmount }} 元</span>
          <span v-else>订单金额百分比 {{ AcitveData.cashAmount }} %</span>
        </el-form-item>
        <el-form-item label="单次最高返现金额：" v-show="AcitveData.cashType == 2">
          <span>{{ utils.formatMoney(AcitveData.maxSingleCash) }} 元</span>
        </el-form-item>
        <el-form-item label="活动总额：">
          <span> {{ Amounts(AcitveData.totalAmount) }}</span>
        </el-form-item>
        <el-form-item label="活动人群：">
          <span>{{ userFormat(AcitveData.targetUsers) }}</span>
        </el-form-item>
        <el-form-item label="有礼单数：">
          <span>{{ AcitveData.maxNumber }}</span>
        </el-form-item>
        <el-form-item label="活动商品：">
          <span>{{ scopeFormat(AcitveData.cashScope) }}</span>
        </el-form-item>
        <el-form-item label="优先级：">
          <span>{{ AcitveData.priority }}</span>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
import { STATUSA, ORDERTYPE, TARGETUSER, TARGESCOPE } from './static'
export default {
  name: 'activityList',
  data() {
    return {
      currentPage: 0,
      params: { limit: 10, page: 1, cashName: '', startTime: '', endTime: '', cashType: '1', cashStatus: '' },
      total: 0,
      AcitveData: {},
      timeinterval: '',
      statusList: STATUSA,
      typeList: ORDERTYPE,
      userList: TARGETUSER,
      scopeList: TARGESCOPE,
      list: [],
      showAcitve: false
    }
  },
  created() {
    this.getList()
  },
  mounted() {},
  methods: {
    getList() {
      if (this.timeinterval && this.timeinterval.length != 0) {
        this.params.startTime = this.timeinterval[0] / 1000
        this.params.endTime = this.timeinterval[1] / 1000
      } else {
        this.params.startTime = ''
        this.params.endTime = ''
      }
      this.$api.get(`/hd/itzReturnCashActivity/page/list`, this.params, r => {
        if (r.status === 200) {
          this.list = r.data.rows
          this.total = r.data.total
        } else {
          this.$message.error({
            title: '错误',
            message: r.message
          })
        }
      })
    },
    clear() {
      this.params = { limit: 10, page: 1, cashName: '', startTime: '', endTime: '', cashType: '1', cashStatus: '' }
      this.timeinterval = ''
      this.currentPage = 1
    },
    search() {
      this.params.page = 1
      this.getList()
    },
    handleCurrentChange(currentPage) {
      this.params.page = currentPage
      this.getList()
    },
    viewDetail(id) {
      this.$router.push({ name: 'activityStatistics', params: { id: id } })
    },
    dateFormat: function(row, column) {
      let date = row[column.property]
      return date ? this.utils.formatDate('YYYY-MM-DD HH:mm', date * 1e3) : '长期有效'
    },
    dateFormat0(time) {
      return time ? this.utils.formatDate('YYYY-MM-DD HH:mm', (time / 1000) * 1e3) : '长期有效'
    },
    statusType: function(row, column) {
      let date = row[column.property]
      let find = this.statusList.find(ele => {
        return ele.id == date
      })
      return find ? find.name : '未知状态'
    },
    typeFormat: function(row, column) {
      let date = row[column.property]
      let find = this.typeList.find(ele => {
        return ele.id == date
      })
      return find ? find.name : '未知类型'
    },
    userFormat(date) {
      let find = this.userList.find(ele => {
        return ele.id == date
      })
      return find ? find.name : '未知类型'
    },
    scopeFormat(date) {
      let find = this.scopeList.find(ele => {
        return ele.id == date
      })
      return find ? find.name : '未知类型'
    },
    acitveDatas(that, id) {
      that.$api.get(`/hd/itzReturnCashActivity/get/detail/${id}`, null, r => {
        if (r.status === 200) {
          that.AcitveData = r.data
          that.AcitveData.startTime = that.AcitveData.startTime * 1000
          that.AcitveData.endTime = that.AcitveData.endTime * 1000
          if (that.AcitveData.cashType === 2) {
            this.Amountflag = true
          }
          //   console.log(that.AcitveData)
        } else {
          that.$message.error({
            title: '错误',
            message: r.message
          })
        }
      })
    },
    ShowAcitves(id) {
      this.showAcitve = true
      let that = this
      this.acitveDatas(that, id)
    },
    EditAcitves(id) {
      this.$router.push({ name: 'editActivity', params: { id: id } })
    },
    stopAct(id) {
      this.$confirm('确定终止本次活动吗?', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        showClose: false,
        customClass: 'closecous',
        center: true
      })
        .then(() => {
          this.$api.get(`/hd/itzReturnCashActivity/end/${id}`, null, r => {
            if (r.status === 200) {
              this.$message('操作成功')
              this.getList()
            } else {
              this.$message.error({
                title: '错误',
                message: r.message
              })
            }
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },
    Amounts(value) {
      return value == '-1' ? '不限' : '￥' + this.utils.formatMoney(value)
    }
  },
  watch: {}
}
</script>
<style lang="scss" scoped>
section {
  background-color: #fff;
  padding: 15px 10px;
  margin-top: 16px;

  h2 {
    margin: 5px 5px;
  }
}
.header {
  margin-bottom: 10px;
}
.container {
  font-size: 15px;
  .title-item {
    vertical-align: middle;
    display: inline-block;
    margin: auto 20px;
  }
  .search-wrapper {
    padding: 10px;
    margin-bottom: 10px;
    background: #fff;
    border: 1px solid #eee;
    .el-form-item {
      margin-bottom: 10px;
      label {
        margin: 10px;
      }
    }
  }
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
.list-wrapper {
  background: #fff;
  padding: 10px;
}
.el-radio-group {
  line-height: 32px;
  width: 100%;
}
.el-picker-panel__footer {
  .el-button--text {
    display: none;
    opacity: 0;
  }
}
.button-wrapper {
  text-align: right;
  padding: 0 20px 10px;
}
.block {
  display: block;
}
/deep/.el-dialog {
  border-radius: 10px 10px 2px 2px;
  overflow: hidden;
  .el-form-item {
    padding: 4px 10px;
    margin-bottom: 0;
    &.el-form-item:nth-child(odd) {
      background: #f3f3f3;
    }
  }
}
/deep/ .el-dialog__header {
  background: #409eff;
  overflow: hidden;
  .el-icon-close:before {
    color: #fff;
  }
  .el-dialog__title {
    color: #fff;
    font-size: 16px;
  }
}
</style>
