<template>
  <div class="container">
    <div class="header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>优惠券</el-breadcrumb-item>
        <el-breadcrumb-item>领券明细</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="coupon-name">优惠券名称：{{ name }}</div>
    <div class="search-wrapper">
      <el-form :inline="true" :model="searchParams" class="demo-form-inline">
        <el-form-item label="用户名称">
          <el-input v-model="searchParams.userName" placeholder="用户名称"></el-input>
        </el-form-item>
        <el-form-item label="优惠券状态">
          <el-select v-model="searchParams.couponStatus" clearable placeholder="优惠券状态">
            <el-option v-for="(value, key) in classList" :key="key" :label="value" :value="key"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
          <el-button @click="clear">清空</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table :data="list" style="width: 100%">
        <el-table-column prop="userName" label="用户"></el-table-column>
        <el-table-column prop="receiveTime" label="领券时间" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="isUse" label="是否使用" :formatter="statusFormat"></el-table-column>
        <el-table-column prop="useTime" label="使用时间" :formatter="dateFormat"></el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="searchParams.pageSize"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: 'couponDetails',
  data() {
    return {
      name: '',
      total: 0,
      currentPage: 1,
      searchParams: { pageNumber: 1, pageSize: 10, couponId: this.$route.params.id, userName: '', couponStatus: '' },
      list: [],
      classList: {
        1: '已使用',
        2: '未使用'
      }
    }
  },
  methods: {
    getList() {
      this.$api.get('hd/itzCouponUsers/user', this.searchParams, res => {
        if (res.status == 200) {
          this.list = res.data.rows
          this.total = res.data.total
          this.name = res.data.couponName
        }
      })
    },
    handleCurrentChange(currentPage) {
      this.searchParams.pageNumber = currentPage
      this.getList()
    },
    clear() {
      this.searchParams.pageNumber = 1
      this.searchParams.userName = ''
      this.searchParams.couponStatus = ''
      this.getList()
    },
    search() {
      this.searchParams.pageNumber = 1
      this.getList()
    },
    statusFormat(row, column, cellValue) {
      return this.classList[cellValue]
    },
    dateFormat: function(row, column, cellValue) {
      return cellValue == 0 ? '' : this.utils.formatDate('YYYY-MM-DD HH:mm:ss', cellValue * 1e3)
    }
  },
  created() {
    this.getList()
  }
}
</script>

<style lang="scss" scoped>
.container {
  font-size: 15px;
  .header {
    margin-bottom: 10px;
  }
  .coupon-name {
    margin-bottom: 10px;
  }
  .search-wrapper {
    padding: 10px;
    margin-bottom: 10px;
    background: #fff;
    border: 1px solid #eee;

    .el-form-item {
      margin-bottom: 0;
    }
  }
}

.el-pagination {
  text-align: center;
  margin-top: 10px;
}
</style>
