<template>
  <div class="container">
    <el-tabs type="card" v-model="activeName">
      <el-tab-pane label="全部商品" name="a" :disabled="isdisabled"></el-tab-pane>
      <el-tab-pane label="已添加商品" name="b" :disabled="!isdisabled"></el-tab-pane>
    </el-tabs>
    <div class="search-wrapper">
      <el-form :inline="true" :model="searchParams" class="demo-form-inline">
        <el-form-item label="商品货号">
          <el-input v-model="searchParams.goodsSn" placeholder="商品货号"></el-input>
        </el-form-item>
        <el-form-item label="商品名称">
          <el-input v-model="searchParams.goodsName" placeholder="商品名称"></el-input>
        </el-form-item>
        <el-form-item label="商家" v-if="merchant">
          <el-input v-model="searchParams.suppliersName" placeholder="商家"></el-input>
        </el-form-item>
        <el-form-item label="商品分类">
          <el-cascader
            v-model="value"
            :options="options"
            @change="handleChange"
            placeholder="商品分类"
            clearable
          ></el-cascader>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
          <el-button @click="clear">清空</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="button-wrapper">
      <el-button type="primary" @click="addMore" v-if="!isdisabled">批量添加</el-button>
      <el-button type="primary" @click="removeMore" v-if="!operate && isdisabled">批量移除</el-button>
    </div>
    <div class="list-wrapper">
      <el-table
        :data="list"
        :row-key="getRowKeys"
        @selection-change="handleSelectionChange"
        ref="multipleTable"
        style="width: 100%"
      >
        <el-table-column
          type="selection"
          :reserve-selection="true"
          :selectable="checkSelectable"
          width="55"
          v-if="!operate"
        ></el-table-column>
        <el-table-column prop="goodsId" label="编号" width="100"></el-table-column>
        <el-table-column prop="goodsName" label="商品名称"></el-table-column>
        <el-table-column prop="goodsSn" label="货号" width="100"></el-table-column>
        <el-table-column prop="marketPrice" label="价格"></el-table-column>
        <el-table-column prop="goodsNumber" label="库存"></el-table-column>
        <el-table-column prop="catName" label="商品分类"></el-table-column>
        <el-table-column prop="sortSales" label="总销量"></el-table-column>
        <el-table-column prop="providerName" label="商家" v-if="merchant"></el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="searchParams.limit"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: 'GoodsOnline',
  props: ['isdisabled', 'couponid', 'operate'],
  data() {
    return {
      total: 12,
      currentPage: 1,
      searchParams: { page: 1, limit: 10, couponId: '', goodsSn: '', goodsName: '', suppliersName: '', catId: '' },
      list: [],
      value: [],
      options: [],
      multipleSelection: [],
      merchant: true,
      getRowKeys(row) {
        return row.goodsId
      }
    }
  },
  computed: {
    //因为activeName的值是通过父组件prop进来的,所以必须提供setter,否则会报错
    activeName: {
      get: function() {
        return this.isdisabled ? 'b' : 'a'
      },
      set: function() {}
    }
  },
  methods: {
    getList() {
      let url = this.isdisabled ? 'hd/itzCouponGoods/page/list' : 'hd/itzGoods/all/page'
      this.$api.get(url, this.searchParams, res => {
        if (res.status == 200) {
          this.total = res.data.total
          this.list = res.data.rows
          this.merchant = res.userType == 1 ? false : true
          this.toggleSelection(this.list)
        }
      })
    },
    getProductList() {
      this.$api.get('hd/itzCoupon/category', null, res => {
        if (res.status == 200) {
          let options = []
          res.data.rows.forEach(value => {
            let obj = {
              value: value.catId,
              label: value.catName
            }
            if (value.itzCategories.length > 0) {
              obj.children = []
              value.itzCategories.forEach(subValue => {
                let subObj = {
                  value: subValue.catId,
                  label: subValue.catName
                }
                if (subValue.itzCategories.length > 0) {
                  subObj.children = []
                  subValue.itzCategories.forEach(sonValue => {
                    let sonObj = {
                      value: sonValue.catId,
                      label: sonValue.catName
                    }
                    subObj.children.push(sonObj)
                  })
                }
                obj.children.push(subObj)
              })
            }
            options.push(obj)
          })
          this.options = options
        }
      })
    },
    handleCurrentChange(currentPage) {
      this.searchParams.page = currentPage
      this.getList()
    },
    handleChange(value) {
      console.log(value)
      this.searchParams.catId = value[value.length - 1]
    },
    checkSelectable(row) {
      //单元格是否可勾选
      return row.check == 2
    },
    toggleSelection(rows) {
      //判断选中状态
      rows.forEach(row => {
        if (row.check == 1) {
          this.$refs.multipleTable.toggleRowSelection(row, true)
        }
      })
    },
    clear() {
      this.searchParams.page = 1
      this.searchParams.goodsName = ''
      this.searchParams.suppliersName = ''
      this.searchParams.goodsSn = ''
      this.searchParams.catId = ''
      this.value = []
      this.getList()
    },
    search() {
      this.searchParams.page = 1
      this.getList()
    },
    handleSelectionChange(val) {
      this.multipleSelection = val
      console.log(this.multipleSelection)
    },
    addMore() {
      if (this.multipleSelection.length == 0) {
        this.$message('请选择至少一条数据')
      } else {
        let arr = []
        this.multipleSelection.forEach((value, index) => {
          let obj = {
            couponId: this.couponid,
            goodsId: value.goodsId
          }
          if (value.check == 2) {
            //过滤之前已选择的数据
            arr.push(obj)
          }
        })
        if (arr.length == 0) {
          this.$message('请选择至少一条数据')
          return
        }
        this.$confirm(`确定添加这${arr.length}个商品吗?`, '批量添加', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        })
          .then(() => {
            this.$api.post('hd/itzCouponGoods/batch', arr, res => {
              if (res.status == 200) {
                this.$emit('add-goods', '已选商品添加成功')
              }
            })
          })
          .catch(() => {
            this.$message('已取消')
          })
      }
    },
    removeMore() {
      if (this.multipleSelection.length == 0) {
        this.$message('请选择至少一条数据')
      } else {
        let arr = []
        this.multipleSelection.forEach((value, index) => {
          arr.push(value.goodsId)
        })
        let ids = arr.join(',')
        this.$confirm(`确定移除这${this.multipleSelection.length}个商品吗?`, '移除', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        })
          .then(() => {
            this.$api.put('hd/itzCouponGoods/goods', { couponId: this.couponid, goodsIds: ids }, res => {
              if (res.status == 200) {
                this.$emit('add-goods', '已选商品移除成功')
              }
            })
          })
          .catch(() => {
            this.$message('已取消')
          })
      }
    }
  },
  created() {
    this.searchParams.couponId = this.couponid
    this.getList()
    this.getProductList()
  }
}
</script>

<style lang="scss" scoped>
.button-wrapper {
  padding: 10px 0;
}
</style>
