<template>
  <div class="container">
    <page-title first-title="代付明细列表"></page-title>
    <div class="button-wrapper">
      <!-- <el-button type="primary" @click="activityAdd()">添加平台</el-button> -->
    </div>
    <div class="search-wrapper">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="浣豆类型">
          <el-select v-model="params.changeType">
            <el-option v-for="item in options" :key="item.value" :label="item.value" :value="item.label"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="订单号">
          <el-input v-model="params.orderId" placeholder="订单号"></el-input>
        </el-form-item>
        <el-form-item label="平台名称">
          <el-input v-model="params.partnerName" placeholder="平台名称"></el-input>
        </el-form-item>
        <el-form-item label="出借人姓名或手机号">
          <el-input v-model="params.nameOrMobile" placeholder="出借人姓名或手机号"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getpaymentDetails()" icon="el-icon-search">搜索</el-button>
          <el-button type="primary" @click="clear()" icon="el-icon-refresh">清空</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table :data="paymentArr" stripe style="width: 100%" :header-cell-style="{ background: '#eef1f6' }">
        <el-table-column prop="orderId" label="订单号"></el-table-column>
        <el-table-column prop="changeToken" label="代付数额"></el-table-column>
        <el-table-column prop="createdAt" label="代付时间">
          <template slot-scope="scope">
            <span>{{ utils.format_date(scope.row.createdAt) }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="userName" label="出借人"></el-table-column>
        <el-table-column prop="mobilePhone" label="手机号"></el-table-column>
        <el-table-column prop="partnerName" label="网贷平台"></el-table-column>
      </el-table>
    </div>
    <!-- 时间紧迫，分页暂时不做 -->
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="params.limit"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: 'RetailActivityList',
  data() {
    return {
      paymentArr: [],
      currentPage: 1,
      params: {
        page: 1,
        limit: 10,
        changeType: '', //数据分类-1全部0:份额变化(包含下单平台代付和充值) 1:浣豆消费 2:签到送豆 3:订单退款
        orderId: '', //订单号
        partnerName: '', //平台姓名
        nameOrMobile: '' //出借人姓名或手机号
        // partnerId:'1'
      },
      total: 0,
      addPlatform_loading: false,
      addPlatform: false,
      options: [
        {
          value: '份额变化(包含下单平台代付和充值)',
          label: '0'
        },
        {
          value: '浣豆消费',
          label: '1'
        },
        {
          value: '签到送豆',
          label: '2'
        },
        {
          value: '订单退款',
          label: '3'
        }
      ]
    }
  },
  created() {
    console.log(this.$route)
    this.params.partnerName = this.$route.params.partners
    this.getpaymentDetails()
  },
  methods: {
    /**
     * 数据分类 -1全部  0:份额变化(包含下单平台代付和充值) 1:浣豆消费 2:签到送豆 3:订单退款
     * private Integer changeType = -1;
     * 流水号 private String pipelineNo;
     * 订单号 private Integer orderId;
     * 平台姓名 private String partnerName;
     * 出借人姓名或手机号  private String nameOrMobile;
     * 平台id private Integer partnerId = -1;
     * private Integer page = 1;
     * private Integer limit = 10;
     */
    getpaymentDetails() {
      this.$api.post('hd/itzUserTokenLog/prepare_detail', this.params, res => {
        if (res.status == 200) {
          this.paymentArr = res.data.rows
          this.total = res.data.total
        }
      })
    },
    handleCurrentChange(currentPage) {
      this.params.page = currentPage
      this.getpaymentDetails()
    },
    // 清空搜索框
    clear() {
      // console.log(Object.values(this.params));
      this.params.page = 1
      this.params.limit = 10
      this.params = {
        page: 1,
        limit: 10,
        changeType: '', //数据分类-1全部0:份额变化(包含下单平台代付和充值) 1:浣豆消费 2:签到送豆 3:订单退款
        orderId: '', //订单号
        partnerName: '', //平台姓名
        nameOrMobile: '', //出借人姓名或手机号
        partnerId: '1'
      }
      this.getpaymentDetails()
    }
  },
  watch: {
    addPlatform(val) {
      if (val === false || val === 'false') {
        console.log('重置add_Platform')
      }
    },
    $route: {
      handler(val) {
        let that = this
        console.log(val, '监控路由变化')
        if (val.name === 'paymentDetails') {
          //在这里做你要做的事
          that.params.partnerName = val.params.partner ? val.params.partner : ''
        }
      },
      deep: true //深度监听
    }
  }
}
</script>

<style lang="scss" scoped>
.button-wrapper {
  // text-align: right;
  padding: 15px 20px;
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
.antion-footer {
  text-align: center;
  margin: 30px 0 0;
}
.el-input--mini .el-input__inner {
  height: 32px;
  line-height: 32px;
}
</style>
