<template>
  <div class="container">
    <section>
      <div class="btn-wrapper">
        <template v-if="isEdit">
          <el-button @click="cancel">取消</el-button>
          <el-button type="primary" class="right" @click="submit" v-if="isPreview">提交并确认</el-button>
          <el-button type="primary" class="right" @click="editDone" v-else>提交并确认</el-button>
        </template>
        <template v-else>
          <el-button @click="goBack">返回</el-button>
          <el-button type="primary" class="right" @click="exportSettlement" v-if="settleInfo.status > 2"
            >打印对账单</el-button
          >
        </template>
      </div>
      <div class="table-head" v-if="!isEdit">
        <h1>状态：{{ title }}</h1>
      </div>
      <div class="table-wrapper">
        <template v-if="!isEdit">
          <el-button class="right" @click="edit" v-if="settleInfo.status < 4 && (role === 'operater' || role === 'all')"
            >编辑</el-button
          >
          <h2 class="t-center">对账单</h2>
          <p class="t-center">
            <label style="margin-right: 20px"> 打款单ID：{{ multi_statement_id }} </label>
            <label>
              结算时间：{{ (settleInfo.settlement_start * 1e3) | convertTime }}——{{
                (settleInfo.settlement_end * 1e3) | convertTime
              }}
            </label>
          </p>
          <div class="t-head">
            <p class="line-item"><label>创建时间：</label>{{ (settleInfo.addtime * 1e3) | convertTime }}</p>
            <p class="line-item"><label>收款单位：</label>{{ settleInfo.pay_company }}</p>
            <p class="line-item"><label>收款人：</label>{{ settleInfo.payee }}</p>
            <p class="line-item">
              <label v-if="settleInfo.remit_type === PAYTYPE.ALIPAY">支付宝账号：</label>
              <label v-else>银行账号：</label>
              {{ settleInfo.remit_account }}
            </p>
          </div>
          <div class="t-body">
            <div class="clearfix">
              <template v-if="settleInfo.commission_type === COMMISSIONTYPE.SLAES">
                <div class="t-body-left">
                  <h2>现金</h2>
                  <p>
                    <span>待确认结算金额（小写）</span
                    ><label>{{ utils.formatMoney(settleInfo.all_cash_income - settleInfo.all_cash_expend) }}</label>
                  </p>
                  <p>
                    <span>销售额佣金（全款）</span><label>{{ utils.formatMoney(settleInfo.sales_service_fee) }}</label>
                  </p>
                  <p>
                    <span>抽取保证金</span><label>{{ utils.formatMoney(settleInfo.bond) }}</label>
                  </p>
                  <p>
                    <span>实际结算金额（小写）</span><label>{{ utils.formatMoney(settleInfo.cash_real_pay) }}</label>
                  </p>
                  <p>
                    <span>金额（大写）</span><label>{{ this.utils.getCapMoney(settleInfo.cash_real_pay) }}</label>
                  </p>
                </div>
                <div class="t-body-right" v-if="cooperateType == 1">
                  <h2>积分</h2>
                  <p>
                    <span>待确认金额（小写）</span
                    ><label>{{ utils.formatMoney(settleInfo.all_huanbi_income - settleInfo.all_huanbi_expend) }}</label>
                  </p>
                  <p>
                    <span>实际结算金额 （小写）</span><label>{{ utils.formatMoney(settleInfo.huanbi_real_pay) }}</label>
                  </p>
                  <p>
                    <span>金额（大写）</span><label>{{ this.utils.getCapMoney(settleInfo.huanbi_real_pay) }}</label>
                  </p>
                </div>
              </template>
              <template v-else>
                <div class="t-body-left">
                  <h2>现金</h2>
                  <p>
                    <span>待确认结算金额（小写）</span
                    ><label>{{ utils.formatMoney(settleInfo.all_cash_income - settleInfo.all_cash_expend) }}</label>
                  </p>
                  <p>
                    <span>扣除佣金（现金）</span><label>{{ utils.formatMoney(settleInfo.cash_service_fee) }}</label>
                  </p>
                  <p>
                    <span>扣除交易费用（现金）</span><label>{{ utils.formatMoney(settleInfo.cash_income_fee) }}</label>
                  </p>
                  <p>
                    <span>抽取保证金（现金）</span><label>{{ utils.formatMoney(settleInfo.bond) }}</label>
                  </p>
                  <p>
                    <span>扣除打款费用（现金）</span><label>{{ utils.formatMoney(settleInfo.cash_remit_fee) }}</label>
                  </p>
                  <p>
                    <span>实际结算金额（小写）</span><label>{{ utils.formatMoney(settleInfo.cash_real_pay) }}</label>
                  </p>
                  <p>
                    <span>金额（大写）</span><label>{{ this.utils.getCapMoney(settleInfo.cash_real_pay) }}</label>
                  </p>
                </div>
                <div class="t-body-right" v-if="cooperateType == 1">
                  <h2>积分</h2>
                  <p>
                    <span>待确认金额（小写）</span
                    ><label>{{ utils.formatMoney(settleInfo.all_huanbi_income - settleInfo.all_huanbi_expend) }}</label>
                  </p>
                  <p>
                    <span>扣除积分佣金（个）</span><label>{{ utils.formatMoney(settleInfo.huanbi_service_fee) }}</label>
                  </p>
                  <p>
                    <span>扣除积分交易费用（人）</span
                    ><label>{{ utils.formatMoney(settleInfo.huanbi_income_fee) }}</label>
                  </p>
                  <p>
                    <span>扣除积分打款费用（人）</span
                    ><label>{{ utils.formatMoney(settleInfo.huanbi_remit_fee) }}</label>
                  </p>
                  <p>
                    <span>实际结算金额 （小写）</span><label>{{ utils.formatMoney(settleInfo.huanbi_real_pay) }}</label>
                  </p>
                  <p>
                    <span>金额（大写）</span><label>{{ this.utils.getCapMoney(settleInfo.huanbi_real_pay) }}</label>
                  </p>
                </div>
              </template>
            </div>
            <p style="display: block;">
              <span style="display: inline-block;width: 150px">备注</span><label>{{ settleInfo.create_log }}</label>
            </p>
          </div>
        </template>

        <template v-else>
          <h2 class="t-center">对账单</h2>
          <div class="t-head">
            <!-- <p class="line-item"><label>创建时间：</label>{{ edit_settleInfo.create_time | convertTime }}</p> -->
            <p class="line-item"><label>收款单位：</label>{{ edit_settleInfo.pay_company }}</p>
            <p class="line-item"><label>收款人：</label>{{ edit_settleInfo.payee }}</p>
            <p class="line-item" v-if="edit_settleInfo.bank">
              <label>银行：</label>{{ bankName(edit_settleInfo.bank) }}
            </p>
            <p class="line-item">
              <label v-if="edit_settleInfo.bank">银行账号：</label>
              <label v-else>支付宝账号：</label>
              {{ edit_settleInfo.remit_account }}
            </p>
          </div>
          <div class="edit-body">
            <!-- 销售额佣金结算 -->
            <template v-if="edit_settleInfo.commission_type === COMMISSIONTYPE.SLAES">
              <p class="line-item" v-if="reduce.cash1 || reduce.cash4">
                <label>结算金额（小写）：</label>
                <el-input v-model="edit_settleInfo.all_cash" disabled v-if="isPreview"></el-input>
                <el-input v-model="real_cash" disabled v-else></el-input>
                <el-checkbox v-model="reduce.cash1">扣除销售额佣金（全款）</el-checkbox>
                <el-checkbox v-model="reduce.cash4">抽取保证金</el-checkbox>
              </p>
              <p class="line-item" v-if="reduce.cash1">
                <label>销售额佣金（全款）：</label><el-input v-model="cash.reduce1" disabled></el-input>
              </p>
              <p class="line-item" v-if="reduce.cash4">
                <label>抽取保证金：</label><el-input v-model="cash.reduce4"></el-input>
              </p>
              <p class="line-item">
                <label>现金结算金额（小写）：</label><el-input v-model="cash_real_pay"></el-input>
                <template v-if="!reduce.cash1 && !reduce.cash4">
                  <el-checkbox v-model="reduce.cash1">扣除销售额佣金（全款）</el-checkbox>
                  <el-checkbox v-model="reduce.cash4">抽取保证金</el-checkbox>
                </template>
              </p>
              <p class="line-item"><label>现金结算金额（大写）：</label>{{ this.utils.getCapMoney(cash_real_pay) }}</p>
            </template>
            <!-- 现金积分佣金结算 -->
            <template v-else>
              <!-- 现金部分 -->
              <template>
                <h3>现金部分</h3>
                <p class="line-item" v-if="reduce.cash1 || reduce.cash2 || reduce.cash3 || reduce.cash4">
                  <label>结算金额（小写）：</label>
                  <el-input v-model="edit_settleInfo.all_cash" disabled v-if="isPreview"></el-input>
                  <el-input v-model="real_cash" disabled v-else></el-input>
                  <el-checkbox v-model="reduce.cash1">扣除佣金</el-checkbox>
                  <el-checkbox v-model="reduce.cash2">扣除交易费用</el-checkbox>
                  <el-checkbox v-model="reduce.cash3">扣除打款费用</el-checkbox>
                  <el-checkbox v-model="reduce.cash4">抽取保证金</el-checkbox>
                </p>
                <p class="line-item" v-if="reduce.cash1">
                  <label>扣除佣金：</label><el-input v-model="cash.reduce1" disabled></el-input>
                </p>
                <p class="line-item" v-if="reduce.cash2">
                  <label>扣除交易费用：</label><el-input v-model="cash.reduce2" disabled></el-input>
                </p>
                <p class="line-item" v-if="reduce.cash3">
                  <label>扣除打款费用：</label><el-input v-model="cash.reduce3" disabled></el-input>
                </p>
                <p class="line-item" v-if="reduce.cash4">
                  <label>抽取保证金：</label><el-input v-model="cash.reduce4"></el-input>
                </p>
                <p class="line-item">
                  <label>现金结算金额（小写）：</label><el-input v-model="cash_real_pay"></el-input>
                  <template v-if="!reduce.cash1 && !reduce.cash2 && !reduce.cash3 && !reduce.cash4">
                    <el-checkbox v-model="reduce.cash1">扣除佣金</el-checkbox>
                    <el-checkbox v-model="reduce.cash2">扣除交易费用</el-checkbox>
                    <el-checkbox v-model="reduce.cash3">扣除打款费用</el-checkbox>
                    <el-checkbox v-model="reduce.cash4">抽取保证金</el-checkbox>
                  </template>
                </p>
                <p class="line-item">
                  <label>现金结算金额（大写）：</label>{{ this.utils.getCapMoney(cash_real_pay) }}
                </p>
              </template>
              <!-- 积分部分 -->
              <template v-if="cooperateType == 1">
                <h3>积分部分</h3>
                <p class="line-item" v-if="reduce.huan1 || reduce.huan2 || reduce.huan3">
                  <label>结算金额（小写）：</label>
                  <el-input v-model="edit_settleInfo.all_huanbi" disabled v-if="isPreview"></el-input>
                  <el-input v-model="real_huan" disabled v-else></el-input>
                  <el-checkbox v-model="reduce.huan1">扣除佣金</el-checkbox>
                  <el-checkbox v-model="reduce.huan2">扣除交易费用</el-checkbox>
                  <el-checkbox v-model="reduce.huan3">扣除打款费用</el-checkbox>
                </p>
                <p class="line-item" v-if="reduce.huan1">
                  <label>扣除佣金：</label><el-input v-model="huanbi.reduce1" disabled></el-input>
                </p>
                <p class="line-item" v-if="reduce.huan2">
                  <label>扣除交易费用：</label><el-input v-model="huanbi.reduce2" disabled></el-input>
                </p>
                <p class="line-item" v-if="reduce.huan3">
                  <label>扣除打款费用：</label><el-input v-model="huanbi.reduce3" disabled></el-input>
                </p>
                <p class="line-item">
                  <label>积分结算金额（小写）：</label><el-input v-model="huanbi_real_pay"></el-input>
                  <template v-if="!reduce.huan1 && !reduce.huan2 && !reduce.huan3">
                    <el-checkbox v-model="reduce.huan1">扣除佣金</el-checkbox>
                    <el-checkbox v-model="reduce.huan2">扣除交易费用</el-checkbox>
                    <el-checkbox v-model="reduce.huan3">扣除打款费用</el-checkbox>
                  </template>
                </p>
                <p class="line-item">
                  <label>积分结算金额（大写）：</label>{{ this.utils.getCapMoney(huanbi_real_pay) }}
                </p>
              </template>
            </template>
            <p class="line-item">
              <label>备注：</label><el-input type="textarea" :rows="3" v-model="edit_settleInfo.create_log"> </el-input>
            </p>
          </div>
        </template>

        <div class="t-bottom clearfix">
          <div class="t-bottom-left">待结算编号</div>
          <p class="t-bottom-right">
            <template v-if="isPreview">
              <span v-for="(item, index) in settleInfo.statement_list" :key="index">{{ item.statement_no }}</span>
            </template>
            <template v-else>
              <span v-for="(item, index) in settleInfo.statement_no" :key="index">{{ item }}</span>
            </template>
          </p>
        </div>
        <div
          style="text-align: right;"
          v-if="!isEdit && settleInfo.status == 3 && (role === 'finance' || role === 'all')"
        >
          <el-input type="textarea" :rows="3" v-model="remit_log"> </el-input>
          <el-button type="primary" @click="confirm" style="margin-top: 15px">确认打款</el-button>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { PAYTYPE, CASHREMITLIMIT, COMMISSIONTYPE } from './static'
import { createStatement, getStatementInfo, updateStatement, confirmRemit, exportSettleList } from '../../api/settle'
import { mapState, mapMutations } from 'vuex'
import { saveAs } from 'file-saver'
export default {
  name: 'SettlementAdd',
  data() {
    return {
      PAYTYPE: PAYTYPE,
      COMMISSIONTYPE: COMMISSIONTYPE,
      multi_statement_id: this.$route.params.id ? this.$route.params.id : '',
      isEdit: true,
      isPreview: true, //是否为预览模式
      cash_real_pay: 0, //实际结算现金
      huanbi_real_pay: 0, //实际结算积分
      edit_settleInfo: {},
      settleInfo: {},
      cash: {
        reduce1: 0,
        reduce2: 0,
        reduce3: 0,
        reduce4: 0
      },
      huanbi: {
        reduce1: 0,
        reduce2: 0,
        reduce3: 0
      },
      reduce: {
        cash1: false,
        cash2: false,
        cash3: false,
        cash4: false,
        huan1: false,
        huan2: false,
        huan3: false
      },
      remit_log: '', //财务确认备注
      againEdit: this.$route.query.isedit || '' // 是否是从待运营确认重新编辑进来
    }
  },
  created() {
    if (this.multi_statement_id) {
      this.isPreview = false
      this.isEdit = false
      this.getStatementInfo()
    } else {
      this.getPreviewInfo()
    }
  },
  watch: {
    sub_settle_cash(newval) {
      this.cash_real_pay = newval
    },
    sub_settle_huan(newval) {
      this.huanbi_real_pay = newval
    }
  },
  computed: {
    ...mapState({
      suppliers_id: state => state.suppliers.suppliers_id,
      preview: state => state.suppliers.preview,
      stateIdList: state => state.suppliers.stateIdList,
      bankList: state => state.settle.bankList,
      role: state => state.settle.role,
      cooperateType: state => state.suppliers.cooperateType
    }),
    title() {
      let title = ''
      if (this.settleInfo.status == 1 || this.settleInfo.status == 2) {
        title = '待商家确认'
      } else if (this.settleInfo.status == 3) {
        title = '商家已确定，待打款'
      } else if (this.settleInfo.status == 4) {
        title = '商家已确定，已打款'
      }
      return title
    },
    real_cash() {
      return this.edit_settleInfo.all_cash_income - this.edit_settleInfo.all_cash_expend
    },
    real_huan() {
      return this.edit_settleInfo.all_huanbi_income - this.edit_settleInfo.all_huanbi_expend
    },
    sub_settle_cash() {
      let sub = this.isPreview ? this.edit_settleInfo.all_cash : this.real_cash
      if (this.reduce.cash1) sub -= this.cash.reduce1
      if (this.reduce.cash2) sub -= this.cash.reduce2
      if (this.reduce.cash3) sub -= this.cash.reduce3
      if (this.reduce.cash4) sub -= this.cash.reduce4
      sub = this.utils.formatFloat(sub, true)
      return sub
    },
    sub_settle_huan() {
      let sub = this.isPreview ? this.edit_settleInfo.all_huanbi : this.real_huan
      if (this.reduce.huan1) sub -= this.huanbi.reduce1
      if (this.reduce.huan2) sub -= this.huanbi.reduce2
      if (this.reduce.huan3) sub -= this.huanbi.reduce3
      sub = this.utils.formatFloat(sub, true)
      return sub
    },
    data() {
      let data = {}
      data['suppliers_statement_id'] = [...this.stateIdList]
      data['cash_real_pay'] = this.cash_real_pay
      data['huanbi_real_pay'] = this.huanbi_real_pay
      data['create_log'] = this.edit_settleInfo.create_log

      if (this.edit_settleInfo.commission_type === COMMISSIONTYPE.SLAES) {
        data['use_sales_service_fee'] = this.reduce.cash1 ? 1 : 0
        data['use_cash_service_fee'] = this.reduce.cash1 ? 0 : 1
      } else {
        data['use_sales_service_fee'] = this.reduce.cash1 ? 0 : 1
        data['use_cash_service_fee'] = this.reduce.cash1 ? 1 : 0
      }
      data['use_cash_income_fee'] = this.reduce.cash2 ? 1 : 0
      data['use_cash_remit_fee'] = this.reduce.cash3 ? 1 : 0
      data['bond'] = this.reduce.cash4 ? this.cash.reduce4 : 0
      data['use_huanbi_service_fee'] = this.reduce.huan1 ? 1 : 0
      data['use_huanbi_income_fee'] = this.reduce.huan2 ? 1 : 0
      data['use_huanbi_remit_fee'] = this.reduce.huan3 ? 1 : 0

      return data
    },
    bondLimit() {
      let bondLimit = this.real_cash - this.cash.reduce1 - this.cash.reduce2 - this.cash.reduce3
      return this.utils.formatFloat(bondLimit, true)
    }
  },
  methods: {
    ...mapMutations({
      saveCooperateType: 'saveCooperateType'
    }),
    getPreviewInfo() {
      this.settleInfo = this.preview
      this.transCash()
      this.transHuanbi()
      this.edit_settleInfo = this.settleInfo
    },
    transCash() {
      if (this.settleInfo.commission_type === COMMISSIONTYPE.SLAES) {
        let cash = this.isPreview
          ? Number(this.settleInfo.all_cash) + Number(this.settleInfo.all_huanbi)
          : Number(this.real_cash) + Number(this.real_huan)
        this.cash.reduce1 = this.utils.formatFloat((cash * this.settleInfo.sales_service_percent) / 100, true)
      } else {
        this.cash.reduce1 = this.transCashItem(this.settleInfo.cash_service_percent)
        this.cash.reduce2 = this.transCashItem(this.settleInfo.cash_income_percent)
        this.cash.reduce3 = this.transCashLimit(this.settleInfo.cash_remit_percent)
      }
    },
    transCashItem(val) {
      let cash = this.isPreview ? this.settleInfo.all_cash : this.real_cash
      return this.utils.formatFloat((cash * val) / 100, true)
    },
    transCashLimit(val) {
      //支付宝下限2元 银行下限1元  上限都是25元
      let cash = this.isPreview ? this.settleInfo.all_cash : this.real_cash
      cash = (cash * val) / 100
      if (this.settleInfo.remit_type === PAYTYPE.BANK) {
        if (cash < CASHREMITLIMIT.BANK.MIN) cash = CASHREMITLIMIT.BANK.MIN
        if (cash > CASHREMITLIMIT.BANK.MAX) cash = CASHREMITLIMIT.BANK.MAX
      } else {
        if (cash < CASHREMITLIMIT.ALIPAY.MIN) cash = CASHREMITLIMIT.ALIPAY.MIN
        if (cash > CASHREMITLIMIT.ALIPAY.MAX) cash = CASHREMITLIMIT.ALIPAY.MAX
      }
      return this.utils.formatFloat(cash, true)
    },
    transHuanbi() {
      if (this.settleInfo.commission_type === COMMISSIONTYPE.SLAES) return
      this.huanbi.reduce1 = this.transHuanbiItem(this.settleInfo.huanbi_service_percent)
      this.huanbi.reduce2 = this.transHuanbiItem(this.settleInfo.huanbi_income_percent)
      this.huanbi.reduce3 = this.transHuanbiItem(this.settleInfo.huanbi_remit_percent)
    },
    transHuanbiItem(val) {
      let huan = this.isPreview ? this.settleInfo.all_huanbi : this.real_huan
      return this.utils.formatFloat((huan * val) / 100, true)
    },

    submit() {
      if (!this.edit_settleInfo.create_log) {
        this.$alert('备注不能为空')
        return
      }
      if (!this.edit_settleInfo.remit_account || !this.edit_settleInfo.pay_company || !this.edit_settleInfo.payee) {
        this.$alert('未设置汇款信息', '', {
          callback: () => {
            this.$router.push({ name: 'SettleSetting' })
          }
        })
        return
      }
      if (this.cash.reduce4 > this.bondLimit) {
        this.$alert('现金结算金额不能为负数')
        return
      }
      this.data.suppliers_id = this.suppliers_id
      createStatement(this.data).then(
        res => {
          this.$alert(res.info)
          this.isPreview = false
          this.isEdit = false
          this.multi_statement_id = res.multi_statement_id
          this.getStatementInfo()
        },
        error => {
          this.$alert(error.errorMsg)
        }
      )
    },
    getStatementInfo() {
      getStatementInfo(this.multi_statement_id).then(
        res => {
          this.settleInfo = res
          this.saveCooperateType(res.cooperate_type)
          if (this.againEdit) {
            this.edit_settleInfo = this.settleInfo
            this.transCash()
            this.transHuanbi()
            this.isEdit = true
          }
        },
        error => {
          console.log(error)
        }
      )
    },
    editDone() {
      this.data.multi_statement_id = this.multi_statement_id
      this.data.statement_no = this.settleInfo.statement_no
      updateStatement(this.data).then(
        res => {
          this.$alert(res.info, '', {
            callback: () => {
              this.goBack()
            }
          })
          this.isEdit = false
          this.getStatementInfo()
        },
        error => {
          this.$alert(error.errorMsg)
        }
      )
    },
    exportSettlement() {
      const loading = this.$loading({
        lock: true,
        text: '下载中',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      exportSettleList(this.multi_statement_id)
        .then(res => {
          const date = this.utils.formatDate('YYYY-MM-DD', new Date().getTime())
          saveAs(new Blob([res.data]), res.filename || this.settleInfo.pay_company + date + '汇总单.pdf')
        })
        .finally(() => {
          loading.close()
        })
    },
    edit() {
      this.edit_settleInfo = this.settleInfo
      this.transCash()
      this.transHuanbi()
      this.isEdit = true
    },
    goBack() {
      this.$router.go(-1)
    },
    cancel() {
      if (this.isPreview || this.againEdit) {
        this.goBack()
      } else {
        this.isEdit = false
      }
    },
    confirm() {
      if (!this.remit_log) {
        this.$alert('请输入打款备注')
        return
      }
      confirmRemit(this.multi_statement_id, this.remit_log).then(
        res => {
          this.$alert(res.info)
          this.$root.reload()
        },
        error => {
          this.$alert(error.errorMsg)
        }
      )
    },
    bankName(val) {
      if (this.bankList.length) {
        let bank = ''
        this.bankList.forEach(item => {
          if (item.short === val) {
            bank = item.name
            return false
          }
        })
        return bank
      } else {
        return val
      }
    }
  }
}
</script>

