<template>
  <div class="container">
    <page-title first-title="账户中心" second-title="积分提现账户" third-title="转账记录"></page-title>
    <section>
        <el-breadcrumb-item>积分提现账户</el-breadcrumb-item>
      <el-table :data="list" style="width: 800px;" v-loading="isLoading">
        <el-table-column prop="addtime" label="申请时间" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="remark" label="备注"></el-table-column>
        <el-table-column prop="huanbi_withdraw" label="金额（元）"></el-table-column>
        <el-table-column prop="current_huanbi_withdraw" label="积分余额"></el-table-column>
      </el-table>
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-size="size"
        layout="prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </section>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { withdrawList } from '../../api/suppliers'
export default {
  name: 'WithdrawalRecord',
  data() {
    return {
      list: [],
      page: 1,
      size: 10,
      total: 0,
      isLoading: false
    }
  },
  created() {
    this.getList()
  },
  computed: {
    ...mapState({
      suppliers_id: state => state.suppliers.suppliers_id
    })
  },
  methods: {
    getList() {
      this.isLoading = true
      withdrawList(this.suppliers_id, this.page)
        .then(
          res => {
            this.total = res.paged.total
            this.list = res.list
            this.list.forEach(item => {
              item.account = '123123'
            })
          },
          error => {
            console.log(error)
          }
        )
        .finally(() => {
          this.isLoading = false
        })
    },
    dateFormat(row, column) {
      let date = row[column.property]
      return this.utils.formatDate('YYYY-MM-DD', date * 1e3)
    },
    priceFormat(row, column) {
      let date = row[column.property]
      return this.utils.formatDate('YYYY-MM-DD', date * 1e3)
    },
    handleCurrentChange(currentPage) {
      this.page = currentPage
      this.getList()
    }
  }
}
</script>

<style lang="scss" scoped>
section {
  background-color: #fff;
  padding: 15px 10px;
  margin-top: 16px;
  h2 {
    margin: 5px 5px;
  }
}
.header {
  margin-bottom: 10px;
}
.el-pagination {
  margin-top: 10px;
}
</style>
