<!-- 结算单列表 -->
<template>
  <div class="container">
    <page-title first-title="结算单列表"></page-title>
    <section class="search-wrapper">
      <el-form :inline="true" :model="params">
        <el-form-item label="结算单状态">
          <el-select v-model="params.status" clearable placeholder="" style="width: 140px">
            <el-option
              v-for="(item, index) in SETTLE_SETTLEMENT_STATUS"
              :key="index"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商家名称" v-if="role !== 'supplier'">
          <el-input v-model="params.suppliers_name" placeholder="请输入商家名称" style="width: 230px"></el-input>
        </el-form-item>
      </el-form>

      <el-form>
        <el-form-item label="结算单生成时间">
          <el-date-picker
            placeholder="开始时间"
            type="date"
            v-model="params.addtime_min"
            style="width: 140px"
            value-format="timestamp"
          ></el-date-picker>
          -
          <el-date-picker
            placeholder="结束时间"
            type="date"
            v-model="params.addtime_max"
            style="width: 140px"
            value-format="timestamp"
          ></el-date-picker>
          <div class="radio-wrapper">
            <el-radio-group size="small" v-model="timeselect" @change="selecttime">
              <el-radio-button v-for="(item, index) in timelist" :key="index" :label="item.value">
                {{ item.name }}
              </el-radio-button>
            </el-radio-group>
          </div>
        </el-form-item>
        <template>
          <el-button type="primary" @click="search">搜索</el-button>
          <el-button @click="clear">清空筛选条件</el-button>
        </template>
      </el-form>
    </section>

    <section>
      <el-table
        :data="listDate"
        style="width: 100%"
        border
        stripe
        :header-cell-style="{ background: '#eef1f6' }"
        v-loading="isLoading"
      >
        <el-table-column prop="multi_log_id" label="结算ID"></el-table-column>
        <el-table-column
          prop="suppliers_name"
          label="商家名称"
          v-if="role !== 'supplier'"
          :key="Math.random()"
        ></el-table-column>
        <el-table-column prop="addtime" label="结算单生成时间" :formatter="timeFormat"></el-table-column>
        <el-table-column prop="cash_pay" label="现金应付金额"></el-table-column>
        <el-table-column prop="cash_real_pay" label="现金实付金额"></el-table-column>
        <el-table-column label="结算时间">
          <template slot-scope="scope">
            {{ dateFormatCol(scope.row.settlement_start) }} --- <br />{{ dateFormatCol(scope.row.settlement_end) }}
          </template>
        </el-table-column>
        <el-table-column prop="status" label="结算单状态" :formatter="statusFormat"></el-table-column>
        <el-table-column prop="endtime" label="打款时间" :formatter="dateFormat"></el-table-column>
        <el-table-column label="打款凭证">
          <template slot-scope="scope">
            <el-button
              v-if="scope.row.status != 1"
              @click="previewProof(scope.row.format_pay_cert)"
              type="text"
              size="small"
              >查看</el-button
            >
          </template>
        </el-table-column>
        <el-table-column prop="remark" label="备注"></el-table-column>
        <el-table-column label="操作" v-if="role !== 'supplier'" :key="Math.random()">
          <template slot-scope="scope">
            <el-button @click="showSupplierInfo(scope.row)" type="text" size="small">商家账号</el-button>
            <el-button
              @click="showMoney(scope.row.multi_log_id)"
              type="text"
              size="small"
              v-if="scope.row.status == 1 && pay_cert_upload_allow"
              >完成</el-button
            >
            <el-button type="text" size="small" @click="printPert(scope.row.multi_log_id)">打印</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page.sync="params.page"
        :page-size="params.size"
        layout="prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </section>

    <!-- 商家账号弹窗 -->
    <div class="suppliers-account">
      <el-dialog
        title="商家收款账号"
        :visible.sync="showAccountInfo"
        center
        width="50%"
        v-if="suppliersInfo.multi_statement_id"
      >
        <div class="account-info" v-if="suppliersInfo.remit_type == 1">
          <p>
            <span class="title">收款人：</span>
            <span class="detail">{{ suppliersInfo.payee }}</span>
          </p>
          <p>
            <span class="title">收款名称：</span>
            <span class="detail">{{ suppliersInfo.pay_company }}</span>
          </p>
          <p>
            <span class="title">支付宝：</span>
            <span class="detail">{{ suppliersInfo.remit_account }}</span>
          </p>
        </div>
        <div class="account-info" v-else>
          <p>
            <span class="title">收款人：</span>
            <span class="detail">{{ suppliersInfo.payee }}</span>
          </p>
          <p>
            <span class="title">收款名称：</span>
            <span class="detail">{{ suppliersInfo.pay_company }}</span>
          </p>
          <p>
            <span class="title">银行名称：</span>
            <span class="detail">{{ suppliersInfo.bank }}</span>
          </p>
          <p>
            <span class="title">开户行：</span>
            <span class="detail">{{ suppliersInfo.open_bank }}</span>
          </p>
          <p>
            <span class="title">银行卡号：</span>
            <span class="detail">{{ suppliersInfo.remit_account }}</span>
          </p>
        </div>
      </el-dialog>
    </div>

    <!-- 完成打款弹窗 -->
    <div class="finish-making-money">
      <el-dialog :visible.sync="showMakeMoney" center width="50%">
        <div class="make-money-info">
          <el-form>
            <el-form-item label="现金实付金额">
              <el-input v-model="submitParams.cash_real_pay"></el-input>
            </el-form-item>
            <el-form-item label="请上传打款凭证">
              <el-upload ref="uploadImg" class="upload-demo" action="#" :auto-upload="false">
                <el-button size="small" type="primary">点击上传</el-button>
              </el-upload>
            </el-form-item>
            <el-form-item label="备注">
              <el-input type="textarea" v-model="submitParams.remark" placeholder="100字以内"></el-input>
            </el-form-item>
          </el-form>
          <div class="btn-box">
            <el-button type="primary" size="small" @click="confirm">确定</el-button>
            <el-button type="default" size="small" @click="showMakeMoney = false">取消</el-button>
          </div>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import { getAllSettlementList, getRole, submitSettlementInfo, printPertImg } from '../../api/settle'
import { SETTLE_SETTLEMENT_STATUS, TIMELIST, STATUSLIST, PAYTYPE, COOPERATE_TYPE } from './static'
import { mapState, mapMutations } from 'vuex'
import { saveAs } from 'file-saver'
export default {
  name: 'WaitSettle',
  data() {
    return {
      showAccountInfo: false,
      showMakeMoney: false,
      SETTLE_SETTLEMENT_STATUS,
      timelist: TIMELIST,
      params: {
        addtime_min: '',
        addtime_max: '',
        suppliers_name: '',
        status: '',
        page: 1,
        size: 10
      },
      total: 0,
      timeselect: '',
      listDate: [],
      suppliersInfo: {},
      isLoading: false,
      // imageUrl: '',
      // 打款提交参数
      submitParams: {
        multi_log_id: '',
        cash_real_pay: '',
        pay_cert: '',
        remark: ''
      },
      pay_cert_upload_allow: false // 是否有上传凭证权限
    }
  },
  computed: {
    ...mapState({
      role: state => state.settle.role,
      cooperateType: state => state.suppliers.cooperateType
    }),
    // 格式化时间戳
    fmtParams() {
      let cloneParams = JSON.parse(JSON.stringify(this.params))
      if (cloneParams.addtime_min.toString().length > 10) {
        cloneParams.addtime_min = parseInt(cloneParams.addtime_min / 1000)
      }
      if (cloneParams.addtime_max.toString().length > 10) {
        cloneParams.addtime_max = parseInt(cloneParams.addtime_max / 1000)
      }
      return cloneParams
    }
  },
  created() {
    this.getSettleList()
    this.getRole()
  },
  methods: {
    ...mapMutations({
      saveRole: 'saveRole',
      saveCooperateType: 'saveCooperateType'
    }),
    getRole() {
      getRole().then(
        res => {
          this.saveRole(res.role)
          if (res.cooperate_type) {
            this.saveCooperateType(res.cooperate_type)
          }
        },
        error => {
          console.log(error)
        }
      )
    },
    getSettleList() {
      this.isLoading = true
      getAllSettlementList(this.fmtParams)
        .then(res => {
          this.listDate = res.list
          this.total = res.paged.total
          this.pay_cert_upload_allow = res.pay_cert_upload_allow
        })
        .finally(() => {
          this.isLoading = false
        })
    },
    // 快捷时间选择
    selecttime(val) {
      let now = new Date().getTime()
      let now0 = new Date(new Date().toLocaleDateString()).getTime() //当天0点时间戳
      let now1 = new Date(new Date().toLocaleDateString()).getTime() - 24 * 3600 * 1e3 //昨天0点时间戳
      let now7 = new Date(new Date().toLocaleDateString()).getTime() - 7 * 24 * 3600 * 1e3 //7天前0点时间戳
      let now30 = new Date(new Date().toLocaleDateString()).getTime() - 30 * 24 * 3600 * 1e3 //30天前0点时间戳
      let start
      let end = now
      if (!val) {
        start = now0
      } else if (val === 1) {
        start = now1
        end = now0 - 1
      } else if (val === 7) {
        start = now7
      } else if (val === 30) {
        start = now30
      }
      this.params.addtime_min = start
      this.params.addtime_max = end
      this.getStateList()
    },
    search() {
      this.params.page = 1
      this.getSettleList()
    },
    clear() {
      this.timeselect = ''
      for (let key in this.params) {
        this.params[key] = ''
      }
      this.params.page = 1
      this.params.size = 10
      this.getSettleList()
    },
    dateFormat(row, column) {
      let date = row[column.property]
      return date > 0 ? this.utils.formatDate('YYYY-MM-DD HH:mm:ss', date * 1e3) : ''
    },
    timeFormat(row, column) {
      let date = row[column.property]
      return date > 0 ? this.utils.formatDate('YYYY-MM-DD', date * 1e3) : ''
    },
    dateFormatCol(date) {
      return this.utils.formatDate('YYYY-MM-DD', date * 1e3)
    },
    statusFormat(row, column, cellValue) {
      let _n = ''
      this.SETTLE_SETTLEMENT_STATUS.forEach(item => {
        if (item.value == cellValue) {
          _n = item.name
        }
      })
      return _n
    },
    // 预览凭证
    previewProof(url) {
      if (url) {
        this.$router.push({ name: 'PreviewPayCert', query: { url: url } })
      } else {
        this.$message.error('未上传打款凭证')
      }
    },
    showSupplierInfo(row) {
      this.showAccountInfo = true
      this.suppliersInfo = row.multi
    },
    showMoney(id) {
      this.showMakeMoney = true
      this.submitParams.multi_log_id = id
    },
    handleCurrentChange() {
      this.getSettleList()
    },
    /*handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },*/
    confirm() {
      let file = this.$refs.uploadImg.uploadFiles
      let reader = new FileReader()
      let _this = this
      reader.addEventListener(
        'load',
        function() {
          _this.submitParams.pay_cert = this.result
          submitSettlementInfo(_this.submitParams).then(
            res => {
              console.log(res)
              if (res) {
                _this.$message.success('打款完成')
                _this.$refs.uploadImg.clearFiles()
                for (let key in _this.submitParams) {
                  _this.submitParams[key] = ''
                }
                _this.showMakeMoney = false
                _this.getSettleList()
              }
            },
            err => {
              _this.$message.error(err.errorMsg)
            }
          )
        },
        false
      )
      reader.readAsDataURL(file[0].raw)
    },
    // 打印
    printPert(id) {
      printPertImg({ multi_log_id: id }).then(res => {
        const date = this.utils.formatDate('YYYY-MM-DD', new Date().getTime())
        saveAs(new Blob([res.data]), res.filename || date + '打款凭证.pdf')
      })
    }
  }
}
</script>
<style lang="scss" scoped>
section {
  background-color: #fff;
  padding: 15px 10px;
  margin-top: 16px;
  h2 {
    margin: 5px 5px;
  }
}
.header {
  margin-bottom: 10px;
}
.radio-wrapper {
  display: inline-block;
  padding: 0 4px;
  border-radius: 4px;
  background-color: #eee;
  margin-left: 10px;
}
.suppliers-account {
  .account-info {
    text-align: center;
    span {
      display: inline-block;
      width: 50%;
    }
    .detail {
      text-align: left;
    }
  }
}
/deep/ .el-form-item__label {
  width: 100%;
  text-align: left;
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
</style>

<style lang="scss">
.finish-making-money {
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409eff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
}
</style>
