<template>
  <div class="container">
    <page-title first-title="活动统计"></page-title>
    <section class="search-wrapper">
      <el-row>
        <div class="grid-content bg-purple">
          活动名称：
          <span>{{ namelist.cashName }}</span>
        </div>
      </el-row>
      <el-row>
        <el-col :span="5">
          <div class="grid-content bg-purple">
            活动总额：
            <span> {{ Amounts(namelist.totalAmount) }}</span>
          </div>
        </el-col>
        <el-col :span="7" v-show="showM">
          <div class="grid-content bg-purple">
            活动所剩余额：
            <span>￥ {{ utils.formatMoney(namelist.surplusAmount) }}</span>
          </div>
        </el-col>
        <el-col :span="4">
          <div class="grid-content bg-purple">
            总订单数：
            <span>{{ namelist.orderTotal }}</span>
          </div>
        </el-col>
        <el-col :span="4">
          <div class="grid-content bg-purple">
            已返现单数：
            <span>{{ namelist.cashTotal }}</span>
          </div>
        </el-col>
        <el-col :span="4">
          <div class="grid-content bg-purple">
            已返现金额：
            <span>￥ {{ utils.formatMoney(namelist.cashAmount) }}</span>
          </div>
        </el-col>
      </el-row>
    </section>
    <div class="search-wrapper">
      <el-form size="small" label-position="left" label-width="100px" v-model="params">
        <el-row :gutter="20">
          <el-col :span="10">
            <el-form-item label="订单号">
              <el-input v-model="params.orderSn" placeholder="精准查询" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label="下单时间">
              <el-date-picker
                v-model="timeinterval"
                value-format="timestamp"
                type="daterange"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :default-time="['00:00:00', '23:59:59']"
                style="width:100%"
              ></el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="10">
            <el-form-item label="手机号">
              <el-input v-model="params.mobile" placeholder="精准查询" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label="返现状态">
              <el-select v-model="params.cashFlag" placeholder="状态">
                <el-option
                  v-for="status in statusList"
                  :key="status.id"
                  :label="status.name"
                  :value="status.id"
                  style="width:100%;"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
          <el-button type="primary" @click="clear">清空</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table :data="list" style="width: 100%">
        <el-table-column prop="orderSn" label="订单号"></el-table-column>
        <el-table-column prop="consignee" label="用户名"></el-table-column>
        <el-table-column prop="mobile" label="手机号"></el-table-column>
        <el-table-column prop="addTime" label="下单时间" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="payTime" label="支付时间" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="returnCash" label="现金红包金额" :formatter="crashFormat"></el-table-column>
        <el-table-column prop="cashFlagStr" label="返现状态"></el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="params.limit"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>
<script>
import { CASHFLAG } from '../static'
export default {
  name: 'activityStatistics',
  data() {
    return {
      currentPage: 0,
      timeinterval: '',
      total: 0,
      showM: false,
      params: {
        id: this.$route.params.id ? this.$route.params.id : '',
        page: 1,
        limit: 10,
        orderSn: '',
        mobile: '',
        cashFlag: '',
        startOrderTime: '',
        endOrderTime: ''
      },
      list: [],
      namelist: [],
      statusList: CASHFLAG
    }
  },
  created() {
    this.getList()
  },
  mounted() {},
  methods: {
    getList() {
      if (this.timeinterval && this.timeinterval.length != 0) {
        this.params.startOrderTime = this.timeinterval[0] / 1000
        this.params.endOrderTime = this.timeinterval[1] / 1000
      } else {
        this.params.startOrderTime = ''
        this.params.endOrderTime = ''
      }
      this.$api.post(`/hd/itzReturnCashActivity/get/records`, this.params, r => {
        if (r.status === 200) {
          this.list = r.data.itzCashActivityRecordsVoList
          this.namelist = r.data
          this.total = r.data.total
        } else {
          this.$message.error({
            title: '错误',
            message: r.message
          })
        }
      })
    },
    clear() {
      this.timeinterval = ''
      this.params = {
        id: this.$route.params.id ? this.$route.params.id : '',
        page: 1,
        limit: 10,
        orderSn: '',
        mobile: '',
        cashFlag: '-1',
        startOrderTime: '',
        endOrderTime: ''
      }
      this.currentPage = 1
      this.total = 0
    },
    search() {
      this.params.page = 1
      this.getList()
    },
    handleCurrentChange(currentPage) {
      this.params.page = currentPage
      this.getList()
    },
    dateFormat: function(row, column) {
      let date = row[column.property]
      return date ? this.utils.formatDate('YYYY-MM-DD HH:mm', date * 1e3) : '未支付'
    },
    crashFormat: function(row, column) {
      let date = row[column.property]
      return date ? '￥ ' + date : date
    },
    Amounts(value) {
      return value == '-1'
        ? ((this.showM = false), '不限')
        : ((this.showM = true), '￥' + this.utils.formatMoney(value))
    }
  },
  watch: {}
}
</script>
<style lang="scss" scoped>
.button-wrapper {
  // text-align: right;
  padding: 15px 20px;
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
.antion-footer {
  text-align: center;
  margin: 30px 0 0;
}
.el-input--mini .el-input__inner {
  height: 32px;
  line-height: 32px;
}
.input-box {
  position: relative;
}
.input-box input {
  opacity: 0;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 50px;
}
.header {
  margin-bottom: 10px;
}
.search-wrapper {
  font-size: 14px;
  line-height: 2em;
  color: #606266;
  padding: 10px;
  margin-bottom: 10px;
  background: #fff;
  border: 1px solid #eee;
  .el-select {
    display: block;
  }
  .el-form-item {
    margin-bottom: 10px;
    label {
      margin: 10px;
    }
  }
}

.list-wrapper {
  background: #fff;
  padding: 10px;
}
</style>
