<template>
  <div class="container">
    <div class="header">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>{{ name }}平台</el-breadcrumb-item>
        <el-breadcrumb-item>浣豆账户明细</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="search-wrapper">
      <el-form :inline="true" :model="searchParams" class="demo-form-inline">
        <el-form-item label="流水号">
          <el-input v-model="searchParams.tradeNo" placeholder="流水号"></el-input>
        </el-form-item>
        <el-form-item label="分类">
          <el-select v-model="searchParams.changeType" clearable placeholder="分类">
            <el-option v-for="(value, key) in classList" :key="key" :label="value" :value="key"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
          <el-button @click="clear">清空</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table :data="list" style="width: 100%">
        <el-table-column prop="createdAt" label="时间" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="pipelineNo" label="流水号"></el-table-column>
        <el-table-column prop="addToken" label="增加浣豆"></el-table-column>
        <el-table-column prop="reduceToken" label="减少浣豆"></el-table-column>
        <el-table-column prop="currentToken" label="余额"></el-table-column>
        <el-table-column prop="changeTypeStr" label="类型"></el-table-column>
      </el-table>
    </div>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="searchParams.limit"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: 'AccountDetails',
  data() {
    let name = localStorage.getItem('partnerName')
    return {
      name: name,
      total: 0,
      currentPage: 1,
      searchParams: { page: 1, limit: 10, tradeNo: '', changeType: '' },
      list: [],
      classList: {
        2: '平台代付',
        3: '平台扣减',
        1: '充值续费'
      }
    }
  },
  methods: {
    getList() {
      this.replaceDatas(this.searchParams)
      this.$api.post('hd/itzPartnerAccountLog/payment_detail', this.searchParams, res => {
        if (res.status == 200) {
          this.list = res.data.rows
          this.total = res.data.total
        }
      })
    },
    replaceDatas(data) {
      for (let key in data) {
        if (data[key] == '' || data[key] == null) {
          delete data[key]
        }
      }
    },
    handleCurrentChange(currentPage) {
      this.searchParams.page = currentPage
      this.getList()
    },
    clear() {
      this.searchParams.page = 1
      this.searchParams.size = 10
      this.searchParams.tradeNo = ''
      this.searchParams.changeType = ''
      this.getList()
    },
    search() {
      this.searchParams.page = 1
      this.getList()
    },
    dateFormat: function(row, column, cellValue) {
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', cellValue * 1e3)
    }
  },
  created() {
    this.getList()
  }
}
</script>

<style lang="scss" scoped>
.container {
  font-size: 15px;
  .header {
    margin-bottom: 10px;
  }
  .title-item {
    vertical-align: middle;
    display: inline-block;
    margin: auto 20px;
  }

  .search-wrapper {
    padding: 10px;
    margin-bottom: 10px;
    background: #fff;
    border: 1px solid #eee;

    .el-form-item {
      margin-bottom: 0;
    }
  }
}

.el-pagination {
  text-align: center;
  margin-top: 10px;
}
</style>
