<template>
  <div class="container">
    <page-title first-title="商家列表" second-title="商家资产"></page-title>
    <section>
      <div class="bond-item">
        <p>保证金</p>
        <h3>{{ bond }}</h3>
      </div>
    </section>

    <section>
      <div style="text-align: right;" v-if="role === 'all' || role === 'operater'">
        <el-button type="primary" @click="showAlert">扣减保证金</el-button>
      </div>

      <el-table
        :data="bondList"
        style="width: 100%"
        v-loading="isLoading"
        :header-cell-style="{ background: '#eef1f6' }"
      >
        <el-table-column prop="addtime" label="申请时间" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="remark" label="备注"></el-table-column>
        <el-table-column prop="bond_money" label="金额（元）" :formatter="priceFormat"></el-table-column>
        <el-table-column prop="current_bond_money" label="保证金余额（元）"></el-table-column>
      </el-table>
    </section>
  </div>
</template>
<script>
import { getSuppliersAccount } from '../../api/suppliers'
import { getBondList, bondReduce } from '../../api/settle'
import { mapState } from 'vuex'
export default {
  name: 'BondInfo',
  data() {
    return {
      bond: 0,
      bondList: [],
      currentPage: 1,
      total: 0,
      cutMoney: 0,
      isLoading: false //是否正在加载
    }
  },
  created() {
    this.getAccount()
    this.getBondList()
  },
  computed: {
    ...mapState({
      suppliers_id: state => state.suppliers.suppliers_id,
      role: state => state.settle.role
    })
  },
  methods: {
    getAccount() {
      getSuppliersAccount(this.suppliers_id).then(
        res => {
          this.bond = res.bond_money
        },
        error => {
          console.log(error)
        }
      )
    },
    getBondList() {
      this.isLoading = true
      getBondList(this.suppliers_id, this.currentPage)
        .then(
          res => {
            this.bondList = res.list
            this.total = res.paged.total
          },
          error => {
            console.log(error)
          }
        )
        .finally(() => {
          this.isLoading = false
        })
    },
    dateFormat: function(row, column) {
      let date = row[column.property]
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', date * 1e3)
    },
    priceFormat(row, column) {
      if (row.direction == 2) {
        return '-' + row[column.property]
      } else {
        return row[column.property]
      }
    },
    showAlert() {
      let h = `<p style="">
                <label style="display:inline-block;width:70px;font-weight:bold">扣减金额</label>
                <input id="bond" name="num"  />
              </p>
              <p style="height:28px;line-height:28px;">
                <label for="bond-add"><input type="radio" name="direction" id="bond-add" value="1" checked>增加</label>
                &ensp;&ensp;
                <label for="bond-sub"><input type="radio" name="direction" id="bond-sub" value="2">减少</label>
              </p>
              <p>
                <label style="display:inline-block;width:70px;font-weight:bold">备注</label>
                <textarea id="remark" name="remark" cols="30" rows="10" style="vertical-align: top;"></textarea>
              </p>`
      this.$alert(h, '', {
        dangerouslyUseHTMLString: true,
        showCancelButton: true,
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            let bond = document.getElementById('bond').value
            let remark = document.getElementById('remark').value
            let directions = document.getElementsByName('direction')
            let direction = 0
            for (let i = 0; i < directions.length; i++) {
              if (directions[i].checked) {
                direction = directions[i].value
              }
            }
            if (!bond || !remark) {
              alert('扣减金额和备注必填')
              return
            }
            bondReduce(this.suppliers_id, bond, direction, remark).then(
              res => {
                console.log(res)
                done()
                this.$root.reload()
              },
              error => {
                alert(error.info)
              }
            )
          } else {
            done()
          }
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
section {
  background-color: #fff;
  padding: 15px 20px;
  margin-top: 16px;
  h2 {
    margin: 5px 5px;
  }
}
.header {
  margin-bottom: 10px;
}
.bond-item {
  width: 100px;
  p,
  h3 {
    margin: 0;
    text-align: center;
  }
  p {
    font-size: 15px;
    margin-bottom: 20px;
  }
}
input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
</style>
