<template>
  <div class="container">
    <page-title first-title="出借人列表"></page-title>
    <div class="button-wrapper">
      <!-- <el-button type="primary" @click="activityAdd()">添加平台</el-button> -->
    </div>
    <div class="search-wrapper">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="网贷平台名称">
          <el-input v-model="params.platformName" placeholder="网贷平台名称"></el-input>
        </el-form-item>
        <el-form-item label="出借人名称">
          <el-input v-model="params.userName" placeholder="出借人名称"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getlenderList()" icon="el-icon-search">搜索</el-button>
          <el-button type="primary" @click="clear()" icon="el-icon-refresh">清空</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table
        :data="lebderArr"
        v-loading="lebder_loading"
        stripe
        style="width: 100%"
        :header-cell-style="{ background: '#eef1f6' }"
      >
        <el-table-column prop="userName" label="姓名"></el-table-column>
        <el-table-column prop="mobilePhone" label="手机号"></el-table-column>
        <el-table-column prop="partnerName" label="网贷平台"></el-table-column>
        <el-table-column prop="totalToken" label="代付额度"></el-table-column>
        <el-table-column prop="useToken" label="已代付数额"></el-table-column>
        <el-table-column prop="token" label="剩余额度"></el-table-column>
        <el-table-column prop="" label="导入时间">
          <template slot-scope="scope">
            <span>{{ utils.format_date(scope.row.createTime) }}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 时间紧迫，分页暂时不做 -->
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="params.limit"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: 'RetailActivityList',
  data() {
    return {
      lebder_loading: false,
      lebderArr: [],
      currentPage: 1,
      params: {
        platformName: '', //网贷平台名称
        userName: '', //出借人名称
        page: 1,
        limit: 10
      },
      total: 0,
      addPlatform_loading: false,
      addPlatform: false
    }
  },
  created() {
    this.params.platformName = this.$route.params.partners
    this.getlenderList()
  },
  methods: {
    /**
     * 去空值
     * @param {*} data
     */
    replaceDatas(data) {
      for (let key in data) {
        if (data[key] == '' || data[key] == null) {
          delete data[key]
        }
      }
    },
    /**
     * 出借人列表
     * @param GET
     * @param {*} url /itzPartnerUser/page/list
     * @param {*} platformName 网贷平台名称
     * @param {*} userName 出借人名称
     * @param {*} page 页数
     * @param {*} limit 条数
     */
    getlenderList() {
      this.lebder_loading = true
      this.replaceDatas(this.params)
      let pramstr = JSON.stringify(this.params)
        .replace(/:/g, '=')
        .replace('{', '')
        .replace('}', '')
        .replace(/,/g, '&')
        .replace(/"/g, '')
        .replace(/\s+/g, '')
      this.$api.get('hd/itzPartnerUser/page/list?' + pramstr, null, res => {
        console.log(res.data)
        if (res.status === 200) {
          this.lebderArr = res.data.rows
          this.total = res.data.total
          this.lebder_loading = false
        }
      })
    },
    /**
     * 分页
     */
    handleCurrentChange(currentPage) {
      this.params.page = currentPage
      this.getlenderList()
    },
    // 清空搜索框
    clear() {
      this.params = {
        platformName: '', //网贷平台名称
        userName: '', //出借人名称
        page: 1,
        limit: 10
      }
      this.getlenderList()
    }
  },
  watch: {
    addPlatform(val) {
      if (val === false || val === 'false') {
        console.log('重置add_Platform')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.button-wrapper {
  // text-align: right;
  padding: 15px 20px;
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
.antion-footer {
  text-align: center;
  margin: 30px 0 0;
}
.el-input--mini .el-input__inner {
  height: 32px;
  line-height: 32px;
}
</style>
