<template>
  <div class="container">
    <page-title first-title="对账单列表"></page-title>
    <section class="search-wrapper">
      <el-form :inline="true" :model="params">
        <el-form-item label="对账单状态">
          <el-select v-model="params.status" clearable placeholder="" style="width: 140px">
            <el-option
              v-for="(item, index) in statusList"
              :key="index"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商家名称" v-if="role !== 'supplier'">
          <el-input v-model="params.suppliers_name" placeholder="请输入商家名称" style="width: 230px"></el-input>
        </el-form-item>
      </el-form>

      <el-form>
        <el-form-item label="对账单生成时间">
          <el-date-picker
            placeholder="开始时间"
            type="date"
            v-model="params.addtime"
            style="width: 140px"
            value-format="timestamp"
          ></el-date-picker>
          -
          <el-date-picker
            placeholder="结束时间"
            type="date"
            v-model="params.endtime"
            style="width: 140px"
            value-format="timestamp"
          ></el-date-picker>
          <div class="radio-wrapper">
            <el-radio-group size="small" v-model="timeselect" @change="selecttime">
              <el-radio-button v-for="(item, index) in timelist" :key="index" :label="item.value">
                {{ item.name }}
              </el-radio-button>
            </el-radio-group>
          </div>
        </el-form-item>
        <template>
          <el-button type="primary" @click="search">搜索</el-button>
          <el-button @click="clear">清空筛选条件</el-button>
        </template>
      </el-form>
    </section>

    <section>
      <el-table
        :data="list"
        style="width: 100%"
        border
        stripe
        :header-cell-style="{ background: '#eef1f6' }"
        v-loading="isLoading"
      >
        <el-table-column prop="multi_statement_id" label="对账ID"></el-table-column>
        <el-table-column prop="suppliers_name" label="商家名称" v-if="role !== 'supplier'"></el-table-column>
        <el-table-column prop="addtime" label="对账单生成时间" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="cash_real_pay" label="实际现金结算金额"></el-table-column>
        <el-table-column prop="huanbi_real_pay" label="实际积分结算金额" v-if="cooperateType == 1"></el-table-column>
        <el-table-column prop="" label="结算时间">
          <template slot-scope="scope">
            {{ dateFormatCol(scope.row.settlement_start) }} --- <br />{{ dateFormatCol(scope.row.settlement_end) }}
          </template>
        </el-table-column>
        <el-table-column prop="status" label="对账单状态" :formatter="fmtStatus"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button @click="viewDetail(scope.row.multi_statement_id)" type="text" size="small">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page.sync="params.page"
        layout="prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </section>
  </div>
</template>
<script>
import { getSettlementList, getRole } from '../../api/settle'
import { SENDSTATUS, TIMELIST, STATUSLIST, PAYTYPE, COOPERATE_TYPE, SETTLE_ACCOUNT_STATUS } from './static'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'WaitSettle',
  data() {
    return {
      params: {
        page: 1,
        size: 10
      },
      statusList: SETTLE_ACCOUNT_STATUS,
      params: {
        addtime: '',
        endtime: '',
        suppliers_name: '',
        status: '',
        page: 1,
        size: 10
      },
      timeselect: '',
      timelist: TIMELIST,
      list: [],
      total: 0,
      isLoading: false //是否正在加载
    }
  },
  computed: {
    ...mapState({
      role: state => state.settle.role,
      cooperateType: state => state.suppliers.cooperateType
    }),
    // 格式化时间戳
    fmtParams() {
      let cloneParams = JSON.parse(JSON.stringify(this.params))
      if (cloneParams.addtime.toString().length > 10) {
        cloneParams.addtime = parseInt(cloneParams.addtime / 1000)
      }
      if (cloneParams.endtime.toString().length > 10) {
        cloneParams.endtime = parseInt(cloneParams.endtime / 1000)
      }
      return cloneParams
    }
  },
  created() {
    this.getSettleList()
    this.getRole()
  },
  methods: {
    ...mapMutations({
      saveRole: 'saveRole',
      saveCooperateType: 'saveCooperateType'
    }),
    getRole() {
      getRole().then(res => {
        this.saveRole(res.role)
        if (res.cooperate_type) {
          this.saveCooperateType(res.cooperate_type)
        }
      })
    },
    getSettleList() {
      this.isLoading = true
      getSettlementList(this.fmtParams)
        .then(res => {
          this.list = res.list
          this.total = res.paged.total
        })
        .finally(() => {
          this.isLoading = false
        })
    },
    // 快捷时间选择
    selecttime(val) {
      let now = new Date().getTime()
      let now0 = new Date(new Date().toLocaleDateString()).getTime() //当天0点时间戳
      let now1 = new Date(new Date().toLocaleDateString()).getTime() - 24 * 3600 * 1e3 //昨天0点时间戳
      let now7 = new Date(new Date().toLocaleDateString()).getTime() - 7 * 24 * 3600 * 1e3 //7天前0点时间戳
      let now30 = new Date(new Date().toLocaleDateString()).getTime() - 30 * 24 * 3600 * 1e3 //30天前0点时间戳
      let start
      let end = now
      if (!val) {
        start = now0
      } else if (val === 1) {
        start = now1
        end = now0 - 1
      } else if (val === 7) {
        start = now7
      } else if (val === 30) {
        start = now30
      }
      this.params.addtime = start
      this.params.endtime = end
      this.getStateList()
    },
    search() {
      this.params.page = 1
      this.getSettleList()
    },
    clear() {
      this.timeselect = ''
      for (let key in this.params) {
        this.params[key] = ''
      }
      this.params.page = 1
      this.params.size = 10
      this.getSettleList()
    },
    dateFormat(row, column) {
      let date = row[column.property]
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', date * 1e3)
    },
    dateFormatCol(date) {
      return this.utils.formatDate('YYYY-MM-DD', date * 1e3)
    },
    fmtStatus(row, column, cellValue) {
      let _n = ''
      this.statusList.forEach(item => {
        if (item.value == cellValue) {
          _n = item.name
        }
      })
      return _n
    },
    viewDetail(id) {
      if (!this.role) return
      this.$router.push({ name: 'waitConfirm', query: { id: id } })
    },
    handleCurrentChange(currentPage) {
      this.params.page = currentPage
      this.getSettleList()
    }
  }
}
</script>
<style lang="scss" scoped>
section {
  background-color: #fff;
  padding: 15px 10px;
  margin-top: 16px;
  h2 {
    margin: 5px 5px;
  }
}
.header {
  margin-bottom: 10px;
}
.radio-wrapper {
  display: inline-block;
  padding: 0 4px;
  border-radius: 4px;
  background-color: #eee;
  margin-left: 10px;
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
</style>
