<!-- 待结算明细 -->
<template>
  <div class="unsettle-detail-container">
    <!-- header -->
    <page-title first-title="账户信息" second-title="待结算明细"></page-title>

    <!-- table 2 -->
    <div class="table-02">
      <el-tabs v-model="currentTab">
        <!-- 收入 -->
        <el-tab-pane label="订单总收入" name="1">
          <v-income :cash="cashTotal" :huanbi="huanbiTotal"></v-income>
          <el-table :data="incomeData" border stripe :header-cell-style="{ background: '#eef1f6' }">
            <el-table-column label="订单号" prop="order_sn"></el-table-column>
            <el-table-column label="下单时间" prop="add_time" :formatter="fmtTime"></el-table-column>
            <el-table-column label="支付金额（元）" prop="settlement_all"></el-table-column>
            <el-table-column label="支付现金额（元）" prop="settlement_cash_no_shipping"></el-table-column>
            <el-table-column label="支付积分额" prop="settlement_huanbi" v-if="cooperateType == 1"></el-table-column>
            <el-table-column label="现金退款" prop="settlement_cash_back"></el-table-column>
            <el-table-column label="积分退款" prop="settlement_huanbi_back" v-if="cooperateType == 1"></el-table-column>
            <el-table-column label="运费" prop="shipping_fee"></el-table-column>
            <el-table-column label="订单状态" prop="status"></el-table-column>
            <el-table-column label="操作">
              <template v-slot="scope">
                <el-button type="text" @click="goDetail(scope.row.order_id)">查看</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>

      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page.sync="params.page"
        layout="prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import UnsettledDetailTotal from './child/UnsettledDetailTotal'
import OrderIncome from './child/OrderIncome'
import { getAccountSettlementData } from '../../api/settle'
import { mapState } from 'vuex'
import { STATUS } from '../suppliers/static'
export default {
  name: '',
  data() {
    return {
      currentTab: '1',
      incomeData: [],
      cashTotal: 0,
      huanbiTotal: 0,
      params: {
        suppliers_id: '',
        page: 1,
        size: 10
      },
      total: 0
    }
  },
  components: { 'v-total': UnsettledDetailTotal, 'v-income': OrderIncome },
  computed: {
    ...mapState({
      suppliers_id: state => state.suppliers.suppliers_id,
      cooperateType: state => state.suppliers.cooperateType
    })
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      this.params.suppliers_id = this.suppliers_id
      getAccountSettlementData(this.params).then(res => {
        this.incomeData = res.list
        this.cashTotal = res.cash
        this.huanbiTotal = res.huanbi
        this.incomeData.forEach(item => {
          item.status = STATUS[item.status]
        })
        this.total = res.paged.total
      })
    },
    goDetail(id) {
      window.location.href = '/admin/order.php?act=info&order_id=' + id
    },
    handleCurrentChange() {
      this.getData()
    },
    fmtTime(row, column, cellValue) {
      return this.utils.formatDate('YYYY-MM-DD HH:mm:ss', cellValue * 1e3)
    }
  }
}
</script>

<style lang="scss" scoped>
.unsettle-detail-container {
  .table-02 {
    margin-top: 20px;
    /deep/ .el-table {
      margin-top: 20px;
    }
  }
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
</style>
